import { getLocaleInfo } from "../../localization";
import { SupportedLocales } from "../../localization/interfaces";
import { ArcgisWebAnalysisError } from "../../errors";

import { CLIENT_THROWN_ERROR, ErrorStrings } from "./interfaces";

/**
 *
 * @param locale
 */
export async function fetchErrorsStrings(locale: SupportedLocales = getLocaleInfo().locale): Promise<ErrorStrings> {
  // rollup does not support dynamic imports on runtime https://github.com/rollup/rollup/issues/2463
  // So we use the imports before run time using this approach , the JSON are built into js files in `dist`.
  // https://devtopia.esri.com/WebGIS/arcgis-charts/pull/1830
  let strings = await import("./errors.t9n.en.json");

  switch (locale) {
    case SupportedLocales.Arabic:
      strings = await import("./errors.t9n.ar.json");
      break;
    case SupportedLocales.Bosnian:
      strings = await import("./errors.t9n.bs.json");
      break;
    case SupportedLocales.Bulgarian:
      strings = await import("./errors.t9n.bg.json");
      break;
    case SupportedLocales.Catalan:
      strings = await import("./errors.t9n.ca.json");
      break;
    case SupportedLocales.Czech:
      strings = await import("./errors.t9n.cs.json");
      break;
    case SupportedLocales.Danish:
      strings = await import("./errors.t9n.da.json");
      break;
    case SupportedLocales.German:
      strings = await import("./errors.t9n.de.json");
      break;
    case SupportedLocales.Greek:
      strings = await import("./errors.t9n.el.json");
      break;
    case SupportedLocales.EnglishUSA:
      strings = await import("./errors.t9n.en-US.json");
      break;
    case SupportedLocales.Spanish:
      strings = await import("./errors.t9n.es.json");
      break;
    case SupportedLocales.Estonian:
      strings = await import("./errors.t9n.et.json");
      break;
    case SupportedLocales.Finnish:
      strings = await import("./errors.t9n.fi.json");
      break;
    case SupportedLocales.French:
      strings = await import("./errors.t9n.fr.json");
      break;
    case SupportedLocales.Hebrew:
      strings = await import("./errors.t9n.he.json");
      break;
    case SupportedLocales.Croatian:
      strings = await import("./errors.t9n.hr.json");
      break;
    case SupportedLocales.Hungarian:
      strings = await import("./errors.t9n.hu.json");
      break;
    case SupportedLocales.Indonesian:
      strings = await import("./errors.t9n.id.json");
      break;
    case SupportedLocales.Italian:
      strings = await import("./errors.t9n.it.json");
      break;
    case SupportedLocales.Japanese:
      strings = await import("./errors.t9n.ja.json");
      break;
    case SupportedLocales.Korean:
      strings = await import("./errors.t9n.ko.json");
      break;
    case SupportedLocales.Lithuanian:
      strings = await import("./errors.t9n.lt.json");
      break;
    case SupportedLocales.Latvian:
      strings = await import("./errors.t9n.lv.json");
      break;
    case SupportedLocales.NorwegianBokml:
      strings = await import("./errors.t9n.nb.json");
      break;
    case SupportedLocales.Dutch:
      strings = await import("./errors.t9n.nl.json");
      break;
    case SupportedLocales.Polish:
      strings = await import("./errors.t9n.pl.json");
      break;
    case SupportedLocales.PortugueseBrazil:
      strings = await import("./errors.t9n.pt-BR.json");
      break;
    case SupportedLocales.PortuguesePortugal:
      strings = await import("./errors.t9n.pt-PT.json");
      break;
    case SupportedLocales.Romanian:
      strings = await import("./errors.t9n.ro.json");
      break;
    case SupportedLocales.Russian:
      strings = await import("./errors.t9n.ru.json");
      break;
    case SupportedLocales.Serbian:
      strings = await import("./errors.t9n.sr.json");
      break;
    case SupportedLocales.Slovak:
      strings = await import("./errors.t9n.sk.json");
      break;
    case SupportedLocales.Slovenian:
      strings = await import("./errors.t9n.sl.json");
      break;
    case SupportedLocales.Swedish:
      strings = await import("./errors.t9n.sv.json");
      break;
    case SupportedLocales.Thai:
      strings = await import("./errors.t9n.th.json");
      break;
    case SupportedLocales.Turkish:
      strings = await import("./errors.t9n.tr.json");
      break;
    case SupportedLocales.Ukrainian:
      strings = await import("./errors.t9n.uk.json");
      break;
    case SupportedLocales.Vietnamese:
      strings = await import("./errors.t9n.vi.json");
      break;
    case SupportedLocales.ChineseChina:
      strings = await import("./errors.t9n.zh-CN.json");
      break;
    case SupportedLocales.ChineseHongKong:
      strings = await import("./errors.t9n.zh-HK.json");
      break;
    case SupportedLocales.ChineseTaiwan:
      strings = await import("./errors.t9n.zh-TW.json");
      break;
    case SupportedLocales.English:
    default:
      break;
  }

  return strings;
}

/**
 * Fetches the error strings to get the error message associated to the provided key, and throws an error.
 * If the message is not found a generic one is used.
 * @param errorKey The key pointing to the message to be used by the error
 * @param {any} [cause] The cause of the error (usually the original error), but can be anything
 * @throws {Error}
 */
export async function throwError(errorKey: keyof ErrorStrings | string, cause?: any): Promise<never> {
  // Fetch error t9n strings based on the locale
  const errorStrings = await fetchErrorsStrings();

  const errorMessage = errorStrings[errorKey as keyof ErrorStrings] as string | undefined;

  throw new ArcgisWebAnalysisError(
    errorKey,
    errorMessage ?? errorStrings.defaultError,
    {
      type: CLIENT_THROWN_ERROR
    },
    cause
  );
}
