import { AnalysisEngine, AnalysisToolData, AnalysisType } from "analysis-ui-schema";

export interface AnalysisHistoryItem {
  toolName: string;
  startTimestamp: number;
  endTimestamp: number | undefined;
  jobParams: AnalysisToolData;
  toolUiParameters: AnalysisToolData;
  analysisEngine: AnalysisEngine;
  analysisType: AnalysisType;
  category: "tools" | "rasterfunction";
  progress?: AnalysisJobProgress;
  jobInfo: __esri.JobInfo;
  results: __esri.ParameterValue[] | undefined;
  resourceName?: string;
  webToolServerUrl?: string;
}

/**
 * The paired down version of the __esri.ParameterValue with all properties
 * stringified and safe to be saved to an item resource. Only intended to be used for saving.
 */
export interface SerializedParameterValue {
  value: string | string[];
  dataType: string;
  paramName: string;
}

export interface SerializedHistoryItem extends Omit<AnalysisHistoryItem, "jobInfo" | "results"> {
  jobInfo: string;
  results: SerializedParameterValue[] | undefined;
}

interface AnalysisJobProgress {
  /**
   *  Represent the progress value as percentage
   * @minimum 0
   * @maximum 1
   * @default 0
   */
  percent: number;
  message: string;
}

export const AnalysisJobStatus = {
  Canceled: "job-cancelled",
  Canceling: "job-cancelling",
  Deleted: "job-deleted",
  Deleting: "job-deleting",
  TimedOut: "job-timed-out",
  Executing: "job-executing",
  Failed: "job-failed",
  New: "job-new",
  Submitted: "job-submitted",
  Succeeded: "job-succeeded",
  Waiting: "job-waiting"
};

export type AnalysisJobStatus = (typeof AnalysisJobStatus)[keyof typeof AnalysisJobStatus];

export const InProgressStatuses = [
  AnalysisJobStatus.Canceling,
  AnalysisJobStatus.Deleting,
  AnalysisJobStatus.Executing,
  AnalysisJobStatus.New,
  AnalysisJobStatus.Submitted,
  AnalysisJobStatus.Waiting
];

export const CompletedStatuses = [
  AnalysisJobStatus.Canceled,
  AnalysisJobStatus.Deleted,
  AnalysisJobStatus.Succeeded,
  AnalysisJobStatus.TimedOut,
  AnalysisJobStatus.Failed
];

/**
 * The interface used to store the analysis history in session storage and an item resource
 */
export interface AnalysisHistory {
  /**
   * The primary data to store and load
   */
  items: SerializedHistoryItem[];
  /**
   * The name of the file suffixing the prefix.
   */
  fileName: string;
  /**
   * The version of AnalysisHistoryItem stored in the file
   */
  version: string;
}

/**
 * The interface used to describe the item retrieved when trying to get licenses of a user
 */
export interface UserLicensedItem {
  itemId: string;
  entitlements: string[];
}

export type SupportedResultItemType = __esri.Layer["type"] | __esri.PortalItem["type"];

export const historyVersion = "1.2.0" as const;
