import {
  AnalysisEngine,
  AnalysisExpression,
  AnalysisToolData,
  AnalysisToolDataItem,
  AnalysisToolUIRule,
  AnalysisType,
  AnalysisValidationRule,
  LocaleItem
} from "analysis-ui-schema";
// common interfaces used by all packages

/**
 * Event payload of job result information from successful job run.
 */
export interface AnalysisJobResults {
  /**
   * Returned results from GP server after a successful job execution
   */
  results: __esri.ParameterValue[];
  /**
   * Information about the GP job that is associated with the results
   */
  jobInfo: __esri.JobInfo;
}

/**
 * Event payload of job status information from job run.
 */
export interface AnalysisGPJobStatus {
  /**
   * Information about the GP job that is being executed, including jobId, jobStatus, Messages, etc.
   */
  jobInfo: __esri.JobInfo;

  /**
   * Data that is passed once per job, only on initial GP job submission in order to create history items
   */
  submissionData?: {
    /**
     * internal tool name used to fetch t9n and render appropriate UI
     */
    toolName: string;
    /**
     * type of analysis engine associated with the tool which created the job
     */
    analysisEngine: AnalysisEngine;
    /**
     * type of analysis category associated with the tool or raster function which created the job
     */
    analysisType: AnalysisType;
    /**
     * All tool parameters that were submitted with the job
     */
    jobParams: AnalysisToolData;
    /**
     * Whatever is in the tool data but not part of job params
     */
    toolUiParameters: AnalysisToolData;

    /**
     * The Url associated with the webtool if the execution is for webtools, otherwise undefined
     */
    webToolServerUrl?: string;
  };
}

/**
 * Multiple functions share these arguments as its passed through
 * Shared interface to use
 */
interface SharedRuleProps {
  /**
   * analysis tool data where a value can be pulled from
   */
  data: AnalysisToolData;
  /**
   * tools rendered params where a value can be pulled from
   */
  renderedParams: Record<string, HTMLElement>;
  /**
   * Instance of the current Analysis Tool.
   */
  currentTool?: HTMLElement;
}

/**
 * Argument types for processRule
 */
export interface ProcessRuleProps extends SharedRuleProps {
  /**
   * rule to process
   */
  rule: AnalysisToolUIRule;
}

/**
 * Argument types for parseValue
 */
export interface ParseValueProps extends SharedRuleProps {
  /**
   * value to parse for the rule
   */
  value: string | AnalysisToolDataItem | null;
}

/**
 * Argument types for evaluate
 */
export interface EvaluateProps extends SharedRuleProps {
  /**
   * expression to be evaluated in rule processing
   */
  expressions: AnalysisExpression | AnalysisExpression[] | null;
}

/**
 * Argument types for processValidationRule
 */
export interface ProcessValidationRuleProps extends SharedRuleProps {
  /**
   * rule to process
   */
  rule: AnalysisValidationRule;
}

