/**
 * ArcGIS Server Response on ArcGIS Enterprise
 * Below is a sample ArcGIS Enterprise request URL used to access an individual server resource:
 * https://machine.domain.com/webadaptor/sharing/rest/portals/0123456789ABCDEF/servers/J5TUOrNQurCDgHFq?f=pjson
 */
