import { AnalysisToolDataItem } from "./analysis-tool-data";
import { AnalysisToolParamDataType } from "./analysis-tool-param-data-type";
import { AnalysisToolParamDirection } from "./analysis-tool-param-direction";
import { AnalysisToolParamFilter } from "./analysis-tool-param-filter";
import { AnalysisToolParamParameterType } from "./analysis-tool-param-parameter-type";

export const AnalysisEngine = {
  Standard: "standard",
  GeoAnalytics: "geoanalytics",
  Raster: "raster",
  Pipelines: "pipelines",
  Session: "session"
} as const;

export const AnalysisType = {
  Tool: "tool",
  RasterFunction: "rft",
  WebTool: "webTool"
} as const;

export type AnalysisEngine = (typeof AnalysisEngine)[keyof typeof AnalysisEngine];
export type AnalysisType = (typeof AnalysisType)[keyof typeof AnalysisType];

/**
 * Explains about the analysis tool with param details from rest api.
 */
export interface AnalysisToolParam {
  /**
   * Name of the input parameter.
   */
  name: string;
  /**
   * Data type of the input parameter.
   */
  dataType: AnalysisToolParamDataType;
  /**
   * Diaplay name (label) of the parameter.
   */
  displayName?: string;
  /**
   * Description of the input param.
   */
  description?: string;
  /**
   * Direction of the output or input.
   */
  direction: AnalysisToolParamDirection;
  /**
   * Default value to be chosen for the parameter.
   */
  defaultValue?: AnalysisToolDataItem;
  /**
   * Explains whether the param is optional or required.
   */
  parameterType?: AnalysisToolParamParameterType;
  /**
   * Showing categories/sections in the tool.
   */
  category?: string;
  /**
   * List of choices for the param value to be chosen.
   */
  choiceList?: string[];
  /**
   * Explains from which param.
   */
  dependency?: string;
  /**
   * Filter for the param.
   */
  filter?: AnalysisToolParamFilter;
  /**
   * Sub-parameter info for compound data types like GP Value Table.
   */
  parameterInfos?: AnalysisToolParam[];
}
