/**
 * Generates and returns a message with the keys in the input statement replaced with new values.
 * @param message
 * @param statement
 * @param parameters
 */
export function formatMessage(statement: Readonly<string>, parameters: Readonly<LocaleItem>): string {
  const templateFn = template(statement);
  return templateFn(parameters);
}

