/**
 * Extracts language portion of locale code
 * Reference: https://github.com/meikidd/locale-code/blob/master/src/index.js#L8
 * Examples:
 * - en-US => en
 * - zh-CN => zh
 * - zh-cn => zh
 * - en => en
 * @param locale
 */
function extractLanguageTag(locale?: string): string {
  const localeCodeRegex = /^([a-z]{2})-([A-Z]{1,2})$/;
  const match = locale?.match(localeCodeRegex);
  let output: string = SupportedLocales.English;
  if (match !== null && match !== undefined && match?.length > 1) {
    output = match[1];
  } else if (locale?.length === 2 && locale.toLowerCase() === locale) {
    output = locale;
  }
  return output;
}

/**
 * Converts a given locale into a supported locale.
 * In case of an unsupported locale, "en" is returned.
 * Examples:
 * - en-US => en-US
 * - zh-CN => zh-CN
 * - en => en
 * - es-AR => es
 * - en-UK => en
 * @param locale
 */
export function convertToSupportedLocale(locale?: string): SupportedLocales {
  let output: string = SupportedLocales.English;
  if (locale !== undefined) {
    const supportedLocales = Object.values(SupportedLocales);
    const hyphenatedSupportedLocales = supportedLocales.filter((ele) => {
      return ele.indexOf("-") !== -1;
    });
    if (locale.indexOf("-") !== -1) {
      const [lang, region] = locale.split("-");
      // To make sure it is valid locale code of form `zh-CN`.
      // In ArcGIS Online user locale codes come with region in lower case `zh-cn`.
      // TODO Remove next Eslint ignore comment.

      locale = `${lang}-${region.toUpperCase()}`;
    }
    if (hyphenatedSupportedLocales.includes(locale as SupportedLocales) === true) {
      output = locale;
    } else {
      const languageTag = extractLanguageTag(locale);
      if (supportedLocales.includes(languageTag as SupportedLocales) === true) {
        output = languageTag;
      }
    }
  }
  return output as SupportedLocales;
}

/**
 * Converts a given locale into a supported format locale.
 * In case of an unsupported locale, "en" is returned.
 * Examples:
 * - en-AU => en-AU
 * - zh-CN => zh-CN
 * - en => en
 * - en-GB => en-GB
 * - it-CH => it-CH
 * - it-ch => it-CH
 * @param locale
 */
export function convertToSupportedFormatLocale(locale?: string): SupportedLocalesForFormats {
  let formatLocale = SupportedLocalesForFormats.find((item) => item === locale);
  if (formatLocale === undefined) {
    formatLocale = locale;
  }
  if (formatLocale !== undefined && formatLocale.indexOf("-") !== -1) {
    const [lang, region] = formatLocale.split("-");
    formatLocale = `${lang}-${region.toUpperCase()}`;
  }
  // Special handling to maintain latn numbering system for arabic locale
  // https://devtopia.esri.com/WebGIS/arcgis-js-api/blob/4master/esri/intl/number.ts#L11
  if (formatLocale === "ar") {
    formatLocale = "ar-u-nu-latn";
  }
  return formatLocale as SupportedLocalesForFormats;
}

/**
 * Helper function to find the closest element crossing multiple (parent) shadowDOM boundaries.
 * The implementation is based on [this](https://stackoverflow.com/questions/54520554/custom-element-getrootnode-closest-function-crossing-multiple-parent-shadowd) solution.
 * @param {string} selector
 * @param {Element} base
 */
export function getClosestElement(selector: string, base: Element): Element | null {
  function closestFrom(el?: Element | Window | Document): Element | null {
    let element;
    if (el === undefined || el === document || el === window) element = null;
    else {
      const found = (el as Element).closest(selector);
      element = found || closestFrom(((el as Element).getRootNode() as ShadowRoot).host);
    }
    return element;
  }
  return closestFrom(base);
}

/**
 * Finds the locale of the closest element to the given element.
 * If no element given, and converts `window.navigator.language` into a supported locale.
 * @param element
 */
export function getLocaleInfo(element: Element = document.documentElement as Element): LocaleInfo {
  const closestElement = getClosestElement("[lang]", element);
  const locale = (closestElement as HTMLElement)?.lang ?? window.navigator.language;
  const convertedLocale = convertToSupportedLocale(locale);
  const formatLocale = convertToSupportedFormatLocale(locale);
  return { locale: convertedLocale, rtl: RTLLocales.includes(convertedLocale), formatLocale };
}

/**
 * Formats a time value in milliseconds since epoch to internatianlized string according to the locale, passing Intl number format options
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/NumberFormat/NumberFormat#options
 * @param {number | string | undefined} value to be formatted and localized
 * @param {Intl.NumberFormatOptions} options options object matching Intl.NumberFormat
 * @returns {string} localized / rounded string
 */
/**
 * formats the timestamp
 * @param timestamp time value in milliseconds since Epoch
 * @param options
 * @returns the formatted timestamp
 */
export function formatTimeToLocale(timestamp?: number, options?: Intl.DateTimeFormatOptions): string {
  const localeCode = getLocaleInfo().formatLocale;
  let formattedValue = "";
  if (!isEmptyDataItem(timestamp) && !Number.isNaN(timestamp)) {
    const dateTimeFormat = new Intl.DateTimeFormat(localeCode, options);
    formattedValue = dateTimeFormat.format(new Date(timestamp as number));
  }
  return formattedValue;
}

/**
 * Fetches the locale file of the given component; defaults to `"en"` locale
 * @param componentName
 * @param props
 * @param locale
 * @param resourcesUrl
 * @param props.path
 * @param props.locale
 * @param props.baseURL
 */
export async function fetchLocaleStrings<T extends LocaleItem>(props: {
  path: string;
  locale: string;
  baseURL: string;
}): Promise<T> {
  const { path, locale, baseURL } = props;
  // Using the getPublicPath method that leverages `data-resources-url` currently this is not working.
  // const rUrl = getPublicPath(componentName) || resourcesUrl;
  let localeData = {} as T;
  try {
    const response = await fetch(`${baseURL}t9n/${path}.t9n.${locale}.json`);
    localeData = await response.json();
  } catch (e) {
    const defaultResponse = await fetch(`${baseURL}t9n/${path}.t9n.${SupportedLocales.English}.json`);
    localeData = await defaultResponse.json();
  }
  return localeData;
}

/**
 * Fetches locale string for the given component
 * @param element
 * @param baseURL
 */
export async function fetchComponentLocaleStrings<T extends LocaleItem>(
  element: Element,
  baseURL: string
): Promise<ComponentLocaleItem<T>> {
  const componentName = element.tagName.toLowerCase();
  const { locale, rtl } = getLocaleInfo(element);
  const strings = await fetchLocaleStrings<T>({ path: componentName, locale, baseURL });
  return { strings, dir: rtl === true ? "rtl" : "ltr", locale: locale };
}

/**
 * Returns if the detected language is rtl or ltr and returns the direction
 * @param element
 */
export function fetchLanguageDirection(element?: Element): "rtl" | "ltr" {
  const { rtl } = getLocaleInfo(element);
  return rtl === true ? "rtl" : "ltr";
}

/**
 * Returns localized data for
 * @param data the data object before localization
 * @param t9nStrings the translation file using which the data will be localized
 */
export function fetchLocalizedMessages<T extends object, U extends keyof T>(data: T, t9nStrings: LocaleItem): T {
  const localizedData = data;
  for (const [key, value] of Object.entries(localizedData)) {
    if (typeof value === "string" && value.includes("$")) {
      // TODO: Simplify to not use unknown
      localizedData[key as U] = t9nStrings[value.replace("$", "")] as unknown as T[U];
    }
  }
  return localizedData;
}

/**
 * update the enterprise help url for demo apps to absolute
 * @param {string} learnMoreUrl the url to be fixed for demo apps
 * @param {string} portalUrl the portal url
 * @returns string updated url for demo app
 */
export function fixUrlForDemoApp(learnMoreUrl: string, portalUrl: string) {
  // Regular expression pattern to match an absolute URL
  const absoluteURLPattern = /^(?:[a-z]+:)?\/\//i;
  // Test if the URL matches the absolute URL pattern
  if (absoluteURLPattern.test(learnMoreUrl) === false) {
    // current App's url
    const currentUrl = window.location.href;
    // Create URL objects for both URLs
    const currentOrigin = new URL(currentUrl).origin;
    const portalOrigin = new URL(portalUrl).origin;
    // for the demo apps or test apps domains would be different
    if (currentOrigin !== portalOrigin) {
      learnMoreUrl = `${portalOrigin}${learnMoreUrl}`;
    }
  }
  return learnMoreUrl;
}

/**
 * Formats a learn more help URL correctly based on the parameters like help base path , portal url.
 * @param {FormatHelpUrlParams} params parameters to this formatLearnMoreHelpUrl
 * @param {string} url the url string from after the locale
 * @param {string} basePath the base path from the signed in portal object.
 * @param {string} locale the locale abbreviation for the browser.
 * @returns {string} the formatted url.
 */
export function formatLearnMoreHelpUrl(params: FormatHelpUrlParams): string {
  const { locale = "en", portalUrl, url } = params;
  let { basePath } = params;
  if (locale === "en-US") {
    basePath = basePath.replace("/en-US/", "/en/");
  }
  const localeInLowerCase = locale.toLocaleLowerCase();
  if (SupportedOnlineHelpLocales.includes(localeInLowerCase) === true) {
    // following regex tests /en/ is present in url and replaces it with the locale
    // https://docdev.arcgis.com/en/arcgis-online/analyze/aggregate-points-mv.htm
    // self Response only provides with `en` locale not as per the user locale or current app locale
    // so we need this replace call.
    basePath = basePath.replace(/^https?:\/\/[^/]+\/en/, (match: string) => match.replace("en", localeInLowerCase));
  }
  // have to pass portal url
  return portalUrl !== undefined ? fixUrlForDemoApp(`${basePath}${url}`, portalUrl) : `${basePath}${url}`;
}

/**
 * Formats a number or string representing a number according to the locale, passing Intl number format options
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/ReferenceGlobal_Objects/Intl/NumberFormat/NumberFormat#options
 * @param {number | string | undefined} value to be formatted and localized
 * @param {Intl.NumberFormatOptions} formatOptions options object matching Intl.NumberFormat
 * @returns {string} localized / rounded string
 */
export function formatNumberToLocale(value?: number | string, formatOptions?: Intl.NumberFormatOptions): string {
  let formattedValue = "";
  if (!isEmptyDataItem(value) && !Number.isNaN(value)) {
    const localeCode = getLocaleInfo().formatLocale;
    formattedValue = Intl.NumberFormat(localeCode, formatOptions).format(Number(value));
  }
  return formattedValue;
}

/**
 * Formats a value of time and a unit according to the locale
 * @param {FormatDurationTimeParams} params parameter object passed for formatting time
 * @param {number} params.value Time value to be formatted
 * @param {Intl.NumberFormatOptions["unit"]} params.unit Unit to use in the relative time internationalized message.
 * [MDN](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/NumberFormat/NumberFormat#unit).
 * @param {Intl.NumberFormatOptions["unitDisplay"]} params.style The length of the internationalized message.
 * [MDN][MDN](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/NumberFormat/NumberFormat#unitDisplay).
 * @returns {string} Internationalized string for the duration of time
 */
function formatDurationTimePart(params: FormatDurationTimeParams): string {
  const { value, unit, style } = params;
  return formatNumberToLocale(value, { style: "unit", unit, unitDisplay: style });
}

/**
 * Formats time duration for a given start and end time value for the provided locale.
 * @param {number} startTime startTime indicated in milliseconds since epoch (UNIX time)
 * @param {number} endTime endTime indicated in milliseconds since epoch (UNIX time)
 * @returns {string} Internationalized string for the elapsed time
 */
export function formatDurationTime(startTime: number, endTime: number): string {
  let time = "";
  const units: DurationTimeUnits = {
    seconds: { single: "second", plural: "seconds" },
    minutes: { single: "minute", plural: "minutes" },
    hours: { single: "hour", plural: "hours" },
    days: { single: "day", plural: "days" }
  };
  const intervals = [
    { unit: units.days, seconds: 86400 },
    { unit: units.hours, seconds: 3600 },
    { unit: units.minutes, seconds: 60 },
    { unit: units.seconds, seconds: 1 }
  ];

  // convert seconds to larger units
  let timeDiffSeconds = (endTime - startTime) / 1000;

  const elapsedTimeParts = intervals.map((interval) => {
    const value = Math.floor(timeDiffSeconds / interval.seconds);
    timeDiffSeconds -= value * interval.seconds;
    return {
      // since we use the
      // [Intl.NumberFormat Unit](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/NumberFormat/NumberFormat#unit
      // it can only take singular time unit forms.
      unit: interval.unit.single,
      value
    };
  });

  // trim 0 values from start of array, but always keep at least one unit
  const firstValue = elapsedTimeParts.findIndex((unit) => unit.value > 0);
  const nonEmptyElapsedTimeParts = elapsedTimeParts.splice(firstValue);
  // format the time
  time = nonEmptyElapsedTimeParts
    .map(
      (timePart) =>
        `${formatDurationTimePart({
          value: timePart.value,
          unit: timePart.unit,
          style: "short"
        })}`
    )
    .join(" ");

  return time;
}
