import Portal from "@arcgis/core/portal/Portal";
import PortalItem from "@arcgis/core/portal/PortalItem";

import { AnalysisEngine, AnalysisToolData, AnalysisToolExecutionType, AnalysisType } from "analysis-ui-schema";

/**
 * Options for credit checks when executing an analysis job
 */
interface CreditCheckOptions {
  /**
   * Optional value to compare with the estimated credits for the analysis job.
   * If the estimated credits are greater than the credit threshold,
   * the user will be notified and the job will not be executed.
   * If the credit threshold is not provided,
   * the threshold check will be skipped.
   */
  creditThreshold?: number;

  /**
   * Indicates whether or not to skip checking if user has enough credits
   * @default false
   */
  skipCreditChecks?: boolean;
}

/**
 *
 * ExecuteProps interface  used as parameter type for execute function.
 * @export
 * @interface ExecuteProps
 */
export interface ExecuteProps {
  portal: Portal;
  toolName: string;
  analysisEngine: AnalysisEngine;
  analysisType: AnalysisType;
  jobParams: AnalysisToolData;
  jobParamsPayload: AnalysisToolData;
  toolUiParameters: AnalysisToolData;
  itemParams?: PortalItem;
  resultParams: string[];
  containerElement: HTMLElement;
  gpMessages?: Record<string, string>;
  webToolServerUrl?: string;
  executionType?: AnalysisToolExecutionType;

  /**
   * Properties used for credit checks when executing an analysis job
   */
  creditCheckOptions?: CreditCheckOptions;
}

/**
 *
 * CheckServiceNameProps interface used as parameter type for checkServiceName function.
 * @export
 * @interface CheckServiceNameProps
 */
export interface CheckServiceNameProps {
  portal: __esri.Portal;
  itemType: string;
  serviceName: string;
}

/**
 * Regex to validate output name characters used in the result service name in Online and Enterprise.
 * In Online, service name should start with alphanumeric and underscore characters, can have spaces in between.
 * space is not allowed as starting or ending characters.
 * In Enterprise, any spaces will be replaced with underscores when creating the service.
 * the regex patterns use \p{L} to match any Unicode letter,
 * \p{N} to match any Unicode number, and \p{L}\p{N} to match alphanumeric characters.
 * The u flag is added at the end of each regex pattern to enable full Unicode matching
 * https://www.regular-expressions.info/unicode.html
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/RegExp/unicode
 * @type {RegExp}
 */
export const validOutputNameCharactersPattern = /^[\p{L}\p{N}_][\p{L}\p{N}_\s]*[\p{L}\p{N}_]$/u;
