import JobInfo from "@arcgis/core/rest/support/JobInfo";
import esriRequest from "@arcgis/core/request";
import * as geoprocessor from "@arcgis/core/rest/geoprocessor";
import { execute } from "@arcgis/core/rest/geoprocessor";
import PortalUser from "@arcgis/core/portal/PortalUser";
import ParameterValue from "@arcgis/core/rest/support/ParameterValue";

import {
  AnalysisCreditInfo,
  AnalysisEngine,
  AnalysisToolData,
  AnalysisToolExecutionType,
  AnalysisToolInfo,
  CostCreditInfo,
  MaxCostCreditInfo,
  RasterAnalysisCostCreditInfo
} from "analysis-ui-schema";

import { getAnalysisServer } from "../tools/index";
import { notifyCreditEstimate, notifyJobStatus } from "../events";

import { CreditEstimateProps, ZeroCreditTools } from "./interfaces";
/**
 * returns the estimated credits for an analysis job that consumes credits.
 * @export
 * @param {CreditEstimateProps} props Props for getCreditsEstimate , see Type for more details.
 * @returns {Promise<ParameterValue[]>}  returns array of ParameterValue (creditResults?.[0]?.value) with only one element whose value type is AnalysisCreditInfo
 */
export async function getCreditsEstimate(props: CreditEstimateProps): Promise<ParameterValue[]> {
  const { portal, jobParams, analysisEngine, toolName: taskName, toolElement: parentComponent } = props;
  const analysisServer = getAnalysisServer(
    analysisEngine ?? AnalysisEngine.Standard,
    portal,
    taskName === "BatchGeocode"
  );
  let results: ParameterValue[] = [];
  const matchingZeroCreditTool = ZeroCreditTools.find(
    (tool) => tool.toolName === taskName && tool.analysisEngine === analysisEngine
  );
  if (matchingZeroCreditTool !== undefined) {
    // TODO: using any as ParameterValue does not have type  for Credit Response Value.
    results = [
      {
        dateType: "string",
        value: {
          cost: 0,
          totalRecords: 0
        }
      } as any
    ];
  } else {
    let estimatorUrl = "";
    switch (analysisEngine) {
      case AnalysisEngine.Raster: {
        estimatorUrl = analysisServer + "/EstimateRasterAnalysisCost";
        break;
      }
      case AnalysisEngine.Standard: {
        estimatorUrl = analysisServer.replace("tasks/GPServer", "Estimate/GPServer/EstimateCredits");
        break;
      }
      default:
        break;
    }
    const taskParameters: AnalysisToolData = {};
    for (const key in jobParams) {
      if (typeof jobParams[key] === "object" && jobParams[key] !== undefined) {
        taskParameters[key] = JSON.stringify(jobParams[key]);
      } else {
        taskParameters[key] = jobParams[key];
      }
    }

    const estimateJobParams =
      analysisEngine === AnalysisEngine.Raster
        ? {
            inputAnalysisTask: {
              name: taskName,
              parameters: taskParameters
            }
          }
        : {
            taskName,
            taskParameters
          };

    const estimateToolJsonResponse = await esriRequest(`${estimatorUrl}?f=json`, { responseType: "json" }); // request is JSAPI module look at the API documentation
    const estimateToolJson: AnalysisToolInfo = estimateToolJsonResponse.data;
    const resultParameters = estimateToolJson?.parameters?.filter(
      (param: any) => param.direction === "esriGPParameterDirectionOutput"
    );
    let response: JobInfo;
    switch (estimateToolJson.executionType) {
      case AnalysisToolExecutionType.Synchronous: {
        response = await execute(estimatorUrl, estimateJobParams);
        // TODO: remove any once ParameterValue supports CreditsResponse type
        results = (response as any).results;
        break;
      }
      case AnalysisToolExecutionType.Asynchronous: {
        const jobInfo = await geoprocessor.submitJob(estimatorUrl, estimateJobParams);
        const options = {
          interval: 1500,
          statusCallback: (j: JobInfo) => {
            notifyJobStatus(parentComponent, { jobInfo: j });
          }
        };
        await jobInfo.waitForJobCompletion(options);
        const resultPromises = resultParameters?.map((param: { name: string }): Promise<ParameterValue> => {
          return jobInfo.fetchResultData(param.name);
        });
        results = await Promise.all(resultPromises as Promise<ParameterValue>[]);

        break;
      }
      default: {
        break;
      }
    }
  }
  if (parentComponent !== undefined) {
    notifyCreditEstimate(parentComponent, results);
  }
  return results;
}

/**
 * Helper to check if the parameterValue is an AnalysisCreditInfo object
 * @param {ParameterValue["value"]} parameterValue the value of the parameter to check
 * @returns {boolean} true if the parameterValue is an AnalysisCreditInfo object, false otherwise
 *
 * FIXME: This should be moved closer to the AnalysisCreditInfo type (and ideally re-worked to check the specific type of AnalysisCreditInfo)
 */
function isAnalysisCreditInfo(parameterValue: unknown): parameterValue is AnalysisCreditInfo {
  const isObject = typeof parameterValue === "object" && parameterValue !== null;

  const hasBasicProperties =
    isObject && "totalRecords" in parameterValue && ("maximumCost" in parameterValue || "cost" in parameterValue);

  const hasAdvancedProperties =
    isObject &&
    "credits" in parameterValue &&
    "functions" in parameterValue &&
    Array.isArray(parameterValue.functions) &&
    parameterValue.functions.every((func) => {
      const isFuncObject = typeof func === "object" && func !== null;
      return isFuncObject && "name" in func && "costFactor" in func && "gigaPixels" in func;
    });

  return isObject && (hasBasicProperties || hasAdvancedProperties);
}

/**
 * Helper to get the estimated credits from the response of getCreditsEstimate
 * @param {ParameterValue[]} creditResults the results from getCreditsEstimate
 * @returns {number | undefined} estimated credits or undefined if unable to parse
 */
export function parseCreditEstimate(creditResults: ParameterValue[]): number | undefined {
  const creditInfo: AnalysisCreditInfo | undefined = isAnalysisCreditInfo(creditResults?.[0]?.value)
    ? creditResults[0].value
    : undefined;

  const estimatedCredits =
    (creditInfo as MaxCostCreditInfo)?.maximumCost ??
    (creditInfo as CostCreditInfo)?.cost ??
    (creditInfo as RasterAnalysisCostCreditInfo)?.credits;

  const result = Number.isNaN(estimatedCredits) ? undefined : estimatedCredits;
  return result;
}

/**
 * Helper to check if the estimated credits is less than the user's credit threshold
 * @param {number | undefined} creditEstimate the estimated credits for the job, undefined if unable to compute
 * @param {number | undefined} creditThreshold the user's credit threshold, undefined if the user's threshold shouldn't be checked
 * @returns {boolean} true if the job should be run, false if the user should be notified and the job should not be run
 */
export function creditThresholdCheck(creditEstimate: number | undefined, creditThreshold: number | undefined): boolean {
  const shouldRun =
    creditThreshold !== undefined && creditEstimate !== undefined ? creditEstimate <= creditThreshold : true;
  return shouldRun;
}

/**
 * checks user has enough credits to run spatial analysis job
 * @param {number | undefined} estimatedCredits the estimated credits for the current job or undefined if unable to compute
 * @param user
 * @returns {boolean} true if the user has enough credits to run the job, false if the user doesn't
 */
export function hasEnoughCredits(estimatedCredits: number | undefined, user: PortalUser): boolean {
  // It is defaulted to true , so even we are unable to compute on client-side, the Analysis Server will
  // check for the user running the job would fail the job when the user does not have enough credits.
  // So our goal is not to obstruct from UI from running analysis when we are unable to compute this operation here.
  let hasCredits = true;
  if (user.sourceJSON.availableCredits !== undefined) {
    if (estimatedCredits !== undefined) {
      hasCredits = user.sourceJSON.availableCredits > estimatedCredits;
    }
  }
  return hasCredits;
}
