import PortalItem from "@arcgis/core/portal/PortalItem";
import request from "@arcgis/core/request";

import memoize from "fast-memoize";

import { PortalHelpMapJSON } from "../interfaces";

export const getPortalItemMemo = memoize(
  (itemId: string, portal?: __esri.Portal): __esri.PortalItem => {
    return new PortalItem({ id: itemId, portal });
  },
  {
    serializer: (args) => `${args[0] as string}-${(args[1] as __esri.Portal | undefined)?.url}`
  }
);

/**
 * Gets the help base url from a portal object
 * @param {__esri.Portal} portal the portal object
 * @returns {string} the help base url
 */
export function getHelpBaseUrl(portal?: __esri.Portal): string {
  return portal?.sourceJSON.helpBase ?? "";
}

/**
 * Updated a portal item based on itemId with the passed in properties
 * @param {string} itemId - itemId to the portal item to be updated
 * @param {Partial<PortalItem>} newProperties - Map of property to update to its new value
 * @returns {Promise<PortalItem>} The updated portal item
 */
export async function updateItemProperties(itemId: string, newProperties: Partial<PortalItem>): Promise<PortalItem> {
  const resultItem = new PortalItem({
    id: itemId
  });
  const loadedPortalItem: PortalItem = await resultItem.load();
  let updatedPortalItem = Object.assign(loadedPortalItem, newProperties);
  updatedPortalItem = await updatedPortalItem.update();
  return updatedPortalItem;
}

/**
 * Check whether ArcGIS Enterprise deployment is on Kubernetes
 * @param {__esri.Portal} portal the portal object
 * @returns {boolean} true if ArcGIS Enterprise deployment is on Kubernetes
 */
export function isArcGISEnterpriseOnKubernetes(portal: __esri.Portal): boolean {
  return portal?.sourceJSON?.portalDeploymentType === "ArcGISEnterpriseOnKubernetes";
}

/**
 * fetches the portal helpMap
 * @param {__esri.Portal} portal the portal instance for which the helpMap to be fetched
 * @returns {Promise<LocaleItem | undefined>} returns the help map when successfully fetched , otherwise undefind
 */
export const getPortalHelpMap = memoize(
  async (portal: __esri.Portal): Promise<PortalHelpMapJSON | undefined> => {
    const { restUrl } = portal;
    const url = `${restUrl}/portals/helpmap`;
    let helpMap: PortalHelpMapJSON | undefined;
    try {
      const fetchPortalHelpMap = await request(url, {
        query: { f: "json" },
        responseType: "json",
        method: "auto"
      });
      helpMap = (await fetchPortalHelpMap.data?.helpMap) ?? undefined;
    } catch (e) {
      helpMap = undefined;
    }
    return helpMap;
  },
  {
    serializer: (args) => `${args[0] as string}-${(args[1] as __esri.Portal | undefined)?.url}`
  }
);
