import FeatureLayer from "@arcgis/core/layers/FeatureLayer";
import GroupLayer from "@arcgis/core/layers/GroupLayer";
import Layer from "@arcgis/core/layers/Layer";
import Field from "@arcgis/core/layers/support/Field";
import PortalItem from "@arcgis/core/portal/PortalItem";
import Renderer from "@arcgis/core/renderers/Renderer";
import FeatureSet from "@arcgis/core/rest/support/FeatureSet";
import esriConfig from "@arcgis/core/config";

import { IField } from "@esri/arcgis-rest-types";
import { memoize } from "lodash-es";

import { FeatureCollection, LocaleItem } from "analysis-ui-schema";

import { isEmptyDataItem, request } from "../common";
import { getLayerLabel } from "../feature-layer";

import { getServiceType } from "./common";
import { SupportedLayer, SupportedResult } from "./interfaces";

/**
 * Determines if the input is a FeatureLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.FeatureLayer} layer is a FeatureLayer
 */
export function isFeatureLayer(layer: __esri.Layer): layer is __esri.FeatureLayer {
  return layer.type === "feature";
}

/**
 * Determines if the input is a FeatureLayer if layer has type 'feature'and has a url
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.FeatureLayer} layer is a FeatureLayer
 */
export function isUrlFeatureLayer(layer: __esri.Layer): layer is __esri.FeatureLayer {
  let isLayerFeature = false;
  if (layer.type === "feature") {
    isLayerFeature = isEmptyDataItem((layer as __esri.FeatureLayer).url) === false;
  }
  return isLayerFeature;
}

/**
 * Determines if the input is a TileLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.TileLayer} layer is a TileLayer
 */
export function isTileLayer(layer: __esri.Layer): layer is __esri.TileLayer {
  return layer.type === "tile";
}

/**
 * Determines if the input is a ImageryLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.ImageryLayer} layer is a ImageryLayer
 */
export function isImageryLayer(layer: __esri.Layer): layer is __esri.ImageryLayer {
  return layer.type === "imagery";
}

/**
 * Determines if the input is a ImageryTileLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.ImageryTileLayer} layer is a ImageryTileLayer
 */
export function isImageryTileLayer(layer: __esri.Layer): layer is __esri.ImageryTileLayer {
  return layer.type === "imagery-tile";
}

/**
 * Determines if the input is a CSVLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.CSVLayer} layer is a CSVLayer
 */
export function isCsvLayer(layer: __esri.Layer): layer is __esri.CSVLayer {
  return layer.type === "csv";
}

/**
 * Determines if the input is a KMLLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.KMLLayer} layer is a KMLLayer
 */
export function isKmlLayer(layer: __esri.Layer): layer is __esri.KMLLayer {
  return layer.type === "kml";
}

/**
 * Determines if the input is a KMLLayer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {layer is __esri.KMLLayer} layer is a KMLLayer
 */
export function isRouteLayer(layer: __esri.Layer): layer is __esri.RouteLayer {
  return layer.type === "route";
}

/**
 * Returns true if the layer is a table, otherwise false.
 * @param {__esri.Layer} layer The layer that you want to check
 * @returns {boolean} True if the layer is a table, otherwise false.
 */
export function isTableLayer(layer: __esri.Layer): boolean {
  return isFeatureLayer(layer) && layer.isTable;
}

/**
 * Returns true if the layer is a group layer, otherwise false.
 * @param {__esri.Layer} layer The layer that you want to check
 * @returns {boolean} True if the layer is a group layer, otherwise false.
 */
export function isGroupLayer(layer: Layer): layer is GroupLayer {
  return layer.type === "group";
}

/**
 * Determines if the input is a sketch layer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {boolean} True if it is a sketch layer
 */
function isSketchLayer(layer: Layer): boolean {
  return layer.type === "map-notes";
}

/**
 * Determines if the input is a Map Image layer
 * @param {__esri.Layer} layer the layer to check the type of
 * @returns {boolean} True if it is a Map Image layer
 */
function isMapImageLayer(layer: Layer): layer is __esri.MapImageLayer {
  return layer.type === "map-image";
}

/**
 * Determines if the input is a Feature Collection JSON
 * @param {SupportedLayer} layer the layer to check the type of
 * @returns {boolean} layer is a Feature Collection
 */
export function isFeatureCollectionJSON(layer: FeatureCollection): boolean {
  return "featureSet" in layer;
}

/**
 * Determines if the feature layer can be used in Extract Data Tool.
 * Extract Data can be done
 * as an admin, you can extract anything in your org
 * as an owner, you can extract anything you own
 * if you don't own or admin, you must have export enabled on the input layer
 * @param {SupportedLayer} layer the layer to check the type of
 * @param {__esri.PortalUser} user the current user accessing the layers
 * @returns {boolean} layer is a Feature Collection
 */
export async function canExtractLayer(layer: SupportedLayer, user?: __esri.PortalUser): Promise<boolean> {
  let canExtract = false;
  let isAdmin = false;
  let isOwner = false;
  // checking if layer has extract capability
  // layer.capabilities JSAPI Capabilities does not have mapping to find Extract so using
  // sourceJSON which represents the layer (service) definition to check capabilities.
  const hasExtractCapability =
    (layer as FeatureLayer).sourceJSON?.capabilities?.toLowerCase().includes("extract") ?? false;
  if (user !== undefined) {
    const { serviceItemId: id } = (layer as FeatureLayer)?.sourceJSON ?? {};
    if (id !== undefined) {
      const portalItem = new PortalItem({ id });
      if (portalItem.loaded === false) {
        await portalItem.load();
      }
      // https://developers.arcgis.com/javascript/latest/api-reference/esri-portal-PortalItem.html#isOrgItem
      // isOrgItem : Indicates whether this item and the user whose credential was used to fetch this item
      // belong to the same ArcGIS Enterprise Portal or ArcGIS Online Organization.
      // using this property we only need to check the owner property for isOwner check
      // and have to check role is admin role for isAdmin check :)
      isOwner = portalItem.isOrgItem && portalItem.owner === user.username;
      isAdmin = portalItem.isOrgItem && user.role === "org_admin";
    }
  }
  // Layer has to be feature layer or table and
  // either user accessing this layer is an admin of that org or owner of that layer or layer is extractable.
  canExtract = (isFeatureLayer(layer) || isTableLayer(layer)) && (isAdmin || isOwner || hasExtractCapability);
  return canExtract;
}

/**
 * Function creating a FeatureLayer from Feature Collection JSON
 * It returns the created Feature Layer.
 * It is memoized (cached) to avoid multiple requests for a same feature layer.
 */
const getFeatureLayerFromFeatureCollection = memoize((featColl: FeatureCollection): FeatureLayer => {
  const inputArgument: __esri.FeatureLayerProperties = {};
  let layerDefinition: FeatureCollection["layerDefinition"] | undefined;
  if (featColl.featureSet !== undefined) {
    inputArgument.source = FeatureSet.fromJSON(featColl.featureSet).features;
    layerDefinition = featColl.layerDefinition;
    // Converting the field using JSAPI Field
    if (layerDefinition.fields !== undefined) {
      inputArgument.fields = layerDefinition.fields.map((field: IField): __esri.Field => Field.fromJSON(field));
    }
    // set optional properties if they are defined
    if (layerDefinition.objectIdField !== undefined) inputArgument.objectIdField = layerDefinition.objectIdField;

    if (layerDefinition.geometryType !== undefined) {
      //FIXME: This conversion below did not work as expected, so mapping each JSON type to JSAPI geometry type
      // const geometry = geometryJsonUtils.fromJSON({ type: layerDefinition.geometryType });
      let type: __esri.FeatureLayerProperties["geometryType"] = "point";
      switch (layerDefinition.geometryType) {
        case "esriGeometryMultipoint":
          type = "multipoint";
          break;
        case "esriGeometryPoint":
          type = "point";
          break;
        case "esriGeometryPolygon":
          type = "polygon";
          break;
        case "esriGeometryPolyline":
          type = "polyline";
          break;
        default:
          break;
      }
      inputArgument.geometryType = type;
    }

    if (layerDefinition.spatialReference !== undefined)
      inputArgument.spatialReference = layerDefinition.spatialReference;

    if (layerDefinition.drawingInfo?.renderer !== undefined)
      inputArgument.renderer = Renderer.fromJSON(layerDefinition.drawingInfo.renderer);
  }
  return new FeatureLayer(inputArgument);
});

/**
 * returns the localized geometry name for a feature layer for known geometry types , otherwise returns undefined.
 * @param {FeatureLayer} featureLayer layer whose geometry name is being constructed
 * @param {Record<string, LocaleItem>} strings list of the t9n common strings which has keys `toolInfoKeys.points/polygons/lines`
 * @returns {string | undefined} localized string or undefined when not found
 */
function getLocalizedGeometryType(featureLayer: FeatureLayer, strings: Record<string, LocaleItem>): string | undefined {
  let geometryTitle: string | undefined;
  // these strings are loaded from analysis-tool-ui-assets common t9n asset, which is passed from the component
  // so unable to use typeof import("./t9n/common.en.json") as we wont know the path for this asset until runtime.
  // points, lines, polygons strings are in toolInfoKeys object in common t9n object.
  // are in https://devtopia.esri.com/WebGIS/arcgis-web-analysis/blob/master/packages/analysis-tool-ui-assets/src/assets/t9n/common.t9n.en.json#L549

  switch (featureLayer.geometryType) {
    case "point":
      geometryTitle = strings.toolInfoKeys.points as string;
      break;
    case "polygon":
      geometryTitle = strings.toolInfoKeys.polygons as string;
      break;
    case "polyline":
      geometryTitle = strings.toolInfoKeys.lines as string;
      break;
  }
  return geometryTitle;
}

/**
 * Returns the feature layers from sketch layer
 * sketch layer is group layer which has multiple sub layers which are graphics layers
 * This functions converts the sketch layer to JSON and then using the featureCollection JSON
 * array it creates feature layer for each sub layer if it has any features.
 * So if there are sub layers with empty features , feature layer is not created for that sub layer.
 * @param {__esri.MapNotesLayer} sketchLayer the sketch leyer passed
 * @param {Record<string, LocaleItem>} strings strings List of t9n strings to update the title of the layers
 * for example in sketch layers layerName (t9NGeometryName)
 * @returns {Promise<FeatureLayer[]>} list of feature layers from sketch layer
 */
export async function getFeatureLayersFromSketchLayer(
  sketchLayer: __esri.MapNotesLayer,
  strings: Record<string, LocaleItem>
): Promise<FeatureLayer[]> {
  await (sketchLayer as any).beforeSave();
  const sketchLayerJson = (sketchLayer as any).toJSON({ origin: "web-map" });
  const nonEmptySketchSubLayers = sketchLayerJson.featureCollection.layers.filter(
    (featColl: FeatureCollection) => featColl.featureSet.features.length > 0 && featColl.layerDefinition.name !== "Text"
  );
  const loadingLayers = nonEmptySketchSubLayers.map(async (featColl: FeatureCollection) => {
    const featureLayer = getFeatureLayerFromFeatureCollection(featColl);
    const supportedGeometryName = getLocalizedGeometryType(featureLayer, strings);
    const layerTitle = getLayerLabel(sketchLayer as SupportedLayer);
    if (supportedGeometryName !== undefined) {
      featureLayer.title = `${layerTitle} (${supportedGeometryName})`;
    } else {
      featureLayer.title = layerTitle;
    }
    await featureLayer.load();
    return featureLayer;
  });
  const featureLayers = await Promise.all(loadingLayers);
  return featureLayers;
}

/**
 * Returns the feature layers from map image layer
 * Map image layer display and analyze data from sublayers defined in a map service,
 * This function creates feature layer for each sub layer and returns them.
 * @param {__esri.MapImageLayer} mapImagelayer the map image leyer passed
 * @returns {Promise<FeatureLayer[]>} list of feature layers from map image layer
 */
async function getFeatureLayersFromMapImageLayer(mapImagelayer: __esri.MapImageLayer): Promise<FeatureLayer[]> {
  // https://next.sites.afd.arcgis.com/javascript/latest/api-reference/esri-layers-MapImageLayer.html#allSublayers
  // using allSubLayers and creating fetaure layers on each sub layer.
  const loadingLayers = mapImagelayer.allSublayers.map(async (subLayer) => {
    // A convenient method for creating a FeatureLayer instance based
    // on the Sublayer's configuration, including dynamic sources.
    // https://next.sites.afd.arcgis.com/javascript/latest/api-reference/esri-layers-support-Sublayer.html#createFeatureLayer
    const featureLayer = await subLayer.createFeatureLayer();
    await featureLayer.load();
    featureLayer.title = getLayerLabel(subLayer);
    return featureLayer;
  });

  const featureLayers = await Promise.all(loadingLayers);
  return featureLayers;
}

/**
 * This function getFeatureLayers is used to convert layers like SketchLayer, KML to
 * feature layers to be used in analysis
 * Analysis tools only work on feature layers or tables or imagery layers.
 * SketchLayer is a custom group layer implementation type __esri.MapNotesLayer so we
 * convert its sub-layers to feature layers(feature collection) to use in analysis
 * The idea behind this function to take supported layers to compatible analysis layers
 * that analysis tools can understand.
 *  Map Viewer/ EXB App -> send a list of layers from Map -> getAnalysisLayers -> provides list of
 * supported analysis layers to use in tools
 * This includes flattening of the layers which is currently done in Map viewer and studio demo app
 * TODO: this is step 1 to this generic function getAnalysisLayers keeping the goal to avoid any regressions.
 * @param {SupportedLayer[]} layers list of supported layers
 * @param {Record<string, LocaleItem>} strings strings List of t9n strings to update the title of the layers
 * for example in sketch layers layerName (t9NGeometryName)
 */
export async function getAnalysisLayers(
  layers: SupportedLayer[],
  strings: Record<string, LocaleItem>
): Promise<SupportedLayer[]> {
  const sketchLayers: SupportedLayer[] = [];
  const mapImageLayers: SupportedLayer[] = [];
  let otherAnalysisLayers: SupportedLayer[] = [];
  layers.forEach((layer) => {
    if (isSketchLayer(layer) === true) {
      sketchLayers.push(layer);
    } else if (isMapImageLayer(layer)) {
      mapImageLayers.push(layer);
    } else {
      otherAnalysisLayers.push(layer);
    }
  });

  const analysisLayers: SupportedLayer[] = [];

  if (sketchLayers.length > 0) {
    const sketchLayersPromiseResult = await Promise.allSettled(
      sketchLayers.map((sketchLayer) => getFeatureLayersFromSketchLayer(sketchLayer as __esri.MapNotesLayer, strings))
    );
    const sketchFeatureLayers = sketchLayersPromiseResult
      .filter((res) => res.status === "fulfilled")
      .map((fulfilledRes: PromiseFulfilledResult<FeatureLayer[]>) => fulfilledRes.value);
    analysisLayers.push(...sketchFeatureLayers.flat());
  }

  if (mapImageLayers.length > 0) {
    const mapImageLayersPromiseResult = await Promise.allSettled(
      mapImageLayers.map((mapImageLayer) => getFeatureLayersFromMapImageLayer(mapImageLayer as __esri.MapImageLayer))
    );
    const mapImageFeatureLayers = mapImageLayersPromiseResult
      .filter((res) => res.status === "fulfilled")
      .map((fulfilledRes: PromiseFulfilledResult<FeatureLayer[]>) => fulfilledRes.value);
    analysisLayers.push(...mapImageFeatureLayers.flat());
  }

  // Ensure that all other analysis layers are loaded or loading has been tried. An unloaded layer will be filtered out in the components
  const otherAnalysisLayersLoadResults: PromiseSettledResult<SupportedLayer>[] = await Promise.allSettled(
    otherAnalysisLayers.map((analysisLayer) =>
      analysisLayer.loaded ? Promise.resolve(analysisLayer) : analysisLayer.load()
    )
  );

  // Filter error load errors to not pass into components
  otherAnalysisLayers = otherAnalysisLayersLoadResults
    .filter((loadResults) => loadResults.status === "fulfilled")
    .map((fulfilledResults: PromiseFulfilledResult<SupportedLayer>) => fulfilledResults.value);

  return [...otherAnalysisLayers, ...analysisLayers];
}

/**
 * Checks if a Supported layer is a supported result
 * @param {__esri.Layer} gpResultValue The layer that you want to check
 * @returns { gpResultValue is SupportedResult} true if the layer is a supported result, otherwise false
 */
export function isSupportedResult(gpResultValue: __esri.Layer | PortalItem): gpResultValue is SupportedResult {
  let isSupported = false;
  const isPortalItem = gpResultValue instanceof PortalItem;
  if (isPortalItem) {
    isSupported = true;
  } else {
    isSupported =
      isFeatureLayer(gpResultValue) ||
      isImageryLayer(gpResultValue) ||
      isTileLayer(gpResultValue) ||
      isImageryTileLayer(gpResultValue) ||
      isRouteLayer(gpResultValue) ||
      isGroupLayer(gpResultValue);
  }

  return isSupported;
}

/**
 * Returns if an object is a supported layer
 * @param {__esri.Layer} layer The layer that you want to check
 * @returns {layer is SupportedLayer} True if the layer is a supported layer, otherwise false
 */
export function isSupportedLayer(layer: __esri.Layer): layer is SupportedLayer {
  return (
    (isFeatureLayer(layer) ||
      isImageryLayer(layer) ||
      isTileLayer(layer) ||
      isImageryTileLayer(layer) ||
      isCsvLayer(layer) ||
      isKmlLayer(layer)) &&
    isRouteLayer(layer) === false
  );
}

/**
 * Gets a server url by removing specific layer information from a layer and leaving only the server portion
 * @param {SupportedLayer} layer the layer to get the server url of
 * @returns {string | undefined} the server url
 */
function getServerUrlFromLayer(layer: SupportedLayer): string | undefined {
  let serverUrl;
  if ("url" in layer && isEmptyDataItem(layer.url) === false) {
    const serviceType = getServiceType(layer.url);
    serverUrl = `${layer.url.split(serviceType)[0]}${serviceType}`;
  }
  return serverUrl;
}

/**
 * Checks via a proxy server if the input layer is accessible and also returns the service url for it
 * @param {string} serverUrl
 * @param {string} portalUrl
 * @returns {Promise<string | undefined>}
 */
const getProxyServiceInfoHelper = memoize(
  async (serverUrl: string, portalUrl?: string): Promise<string | undefined> => {
    let status = false;
    const url = window.location.href;
    // NOTE: This is done for local development environments so they can function without a FQDN, but should not affect
    // deployed apps.
    if (url.includes("://localhost:") === true || portalUrl === undefined) {
      status = true;
    } else {
      const serviceUrl = `${serverUrl}${serverUrl.indexOf("?") > -1 ? "&" : "?"}f=json`;
      esriConfig.request.proxyUrl = `${portalUrl}/sharing/proxy`;
      try {
        const response = await request(serviceUrl, { useProxy: true });
        status = isEmptyDataItem(response.data) === false;
      } catch (exception) {
        status = false;
      }
    }

    return status === true ? serverUrl : undefined;
  }
);

/**
 * Gets the proxy service info but attaches a layerId to the response
 * @param {SupportedLayer} layer layer being accessed via proxy service
 * @param {string} portalUrl current portal url
 * @returns {Promise<string | undefined>} layer.id is returned if layer was accessible
 */
export /** */
const getProxyServiceInfo = async (layer: SupportedLayer, portalUrl?: string): Promise<string | undefined> => {
  let status;
  const serverUrl = getServerUrlFromLayer(layer);

  if (serverUrl !== undefined) {
    status = await getProxyServiceInfoHelper(serverUrl, portalUrl);
  } else {
    status = layer.id;
  }

  return status !== undefined ? layer.id : undefined;
};
