import { get, omit, omitBy, pick, set } from "lodash-es";

import {
  AnalysisToolData,
  AnalysisToolDataItem,
  AnalysisToolInfo,
  AnalysisToolParam,
  AnalysisToolParamDirection,
  AnalysisToolUIParam,
  AnalysisUIParamProps,
  ItemId,
  OutputName,
  ParamViewModelKey
} from "analysis-ui-schema";

import { isEmptyDataItem, removeEmptyObjects } from "../common";
import { AnalysisSupportedItemTypes, AnalyzeGeocodeResult, ProcessedDataProperties } from "../interfaces";
import { serializers } from "../serialization";

import { sanitizeUrl } from "./common";
import { getUIParamsByName } from "./tool-ui-helpers";
import {
  AnalysisToolGlobalData,
  CalculatedParameterValueKeys,
  FilterAndRenameParamsOptions,
  PrepJobParamsForSubmissionOptions
} from "./interfaces";

/**
 * Takes the job params, and calculates any values that need to be calculated at job submission time
 * @param {AnalysisToolData} jobParams the job params object for which values to be calculated
 * @returns {AnalysisToolData} returns the job data
 */
export function calculateParameterValues(jobParams: AnalysisToolData): AnalysisToolData {
  const calculatedJobParams = { ...jobParams };
  for (const param in calculatedJobParams) {
    switch (calculatedJobParams[param]) {
      case CalculatedParameterValueKeys.CurrentTime:
        calculatedJobParams[param] = new Date().getTime();
        break;
      default:
        break;
    }
  }
  return calculatedJobParams;
}

/**
 * Helper to replace a string's spaces with underscores
 * @param {string} value the string to replace spaces in
 * @returns {string} the string with spaces replaced with underscores
 */
export function replaceSpacesWithUnderscores(value: string): string {
  return value.replace(/\s/g, "_");
}

/**
 * Replaces spaces with underscores when the portal is not ArcGIS Online
 * @param {string} outputNameValue the output name parameter value to format (JSON string)
 * @param {boolean} isPortal true if running on on-premises portal
 * @returns {string} the output name parameter value formatted for the portal
 * @throws {Error} if the output name parameter value is not valid JSON
 */
export function formatOutputNameForPortal(outputNameValue: string, isPortal = false): string {
  let formattedOutputName = outputNameValue;

  if (isPortal === true) {
    // Will throw an error if the output name parameter value is not valid JSON
    const outputName = JSON.parse(outputNameValue);

    // Define a map of potential sources for the service name (property path -> value)
    const potentialServiceNamePaths = {
      "serviceProperties.name": outputName.serviceProperties?.name,
      "itemProperties.title": outputName.itemProperties?.title
    };

    // Find the first source that has a value
    const [serviceNameSource, serviceName] =
      Object.entries(potentialServiceNamePaths).find(([_key, value]) => value !== undefined) ?? [];

    // Replace spaces with underscores
    if (serviceNameSource !== undefined) {
      const formattedServiceName = replaceSpacesWithUnderscores(serviceName);
      set(outputName, serviceNameSource, formattedServiceName);
    }

    formattedOutputName = JSON.stringify(outputName);
  }

  return formattedOutputName;
}

/**
 * Gets the actual output parameter names, handling special cases / multi
 * @param {string[]} parameterNames the names to parse
 * @returns {string[]} parsed names containing only the output names
 */
export function getActualOutputNames(parameterNames?: string[]): string[] {
  // TODO: Come back to this, need more robust pattern as tool list grows
  // This works for min-ship tools
  let outputParamNames = ["outputName"];
  // Special case for CreateViewshed (RA) tool
  // FIXME: Hardcoded special case here, we can use a master list if we need
  if (parameterNames?.includes("aboveGroundLevelOutputName") === true)
    outputParamNames.push("aboveGroundLevelOutputName");

  if (parameterNames?.includes("outputName") === false) {
    // We have a non-standard or multiple output, handle as a special case
    outputParamNames = parameterNames.filter((name) => name.toLowerCase().includes("output"));
  }
  return outputParamNames;
}

/**
 * Returns the job parameters based on a toolJSON from a data object.
 * @param {AnalysisToolInfo} toolJSON the tool json for which we are getting the job parameters
 * @param {AnalysisToolGlobalData} data complete current tool data including empty values
 * @returns {AnalysisToolGlobalData} provides the job parameters for current ui state
 */
export function getJobParams(toolJSON: AnalysisToolInfo, data: AnalysisToolGlobalData): AnalysisToolGlobalData {
  const { parameters } = toolJSON;
  const parameterNames = parameters
    ?.filter((parameter: AnalysisToolParam) => parameter.direction !== AnalysisToolParamDirection.Output)
    ?.map((parameter: AnalysisToolParam) => parameter.name);
  let jobParams = pick(data, parameterNames as string[]);
  jobParams = removeEmptyObjects<AnalysisToolGlobalData>(jobParams);
  return jobParams;
}

/**
 * Filters and renames keys in the job parameters based on their visibility and presence in the UI parameter model.
 *
 * If a key in job parameters is not present in the UI parameter view model, then it will be removed from the job parameters.
 * If a key is present in the UI parameter view model, is visible, and has a 'submissionName', then depending on the value of
 * 'useSubmissionName' (default is true), the function behaves as follows:
 *    - If 'useSubmissionName' is true, the key will be added/updated in the job parameters at the 'submissionName' key.
 *      The original key will then be removed from the job parameters.
 *    - If 'useSubmissionName' is false, the key will be added/updated in the job parameters at the original key. The 'submissionName'
 *      key will then be removed from the job parameters.
 * If a key is present in UI parameter model but does not have a 'submissionName', then it will be left as is in the job parameters.
 * @param {object} options - The options object.
 * @param {AnalysisToolData | undefined} options.jobParams - The job parameters to be filtered and potentially renamed.
 * @param {AnalysisToolData | undefined} options.UIOnlyParams - UI specific parameters that may or may not have a corresponding value in jobParams.
 * @param {AnalysisParamModel | undefined} options.toolUIParamModel - The UI parameter model used for determining key visibility and potential renaming.
 * @param {boolean} options.useSubmissionName - Whether or not to use the submissionName or original key when renaming. Defaults to true.
 * @returns {AnalysisToolData} The updated job parameters with keys filtered and renamed as per visibility and presence in the UI parameter model.
 */
export function filterAndRenameParams({
  jobParams,
  UIOnlyParams,
  toolUIParamModel,
  useSubmissionName = true
}: FilterAndRenameParamsOptions): AnalysisToolData {
  const updatedParams: AnalysisToolData = { ...jobParams };

  if (isEmptyDataItem(toolUIParamModel) === false) {
    // Use a temporary object to store the renamed parameters
    // this prevents the edge case where a parameter is renamed to a job param that is later removed
    const renamedParams: AnalysisToolData = {};

    for (const [key, value] of Object.entries({ ...jobParams, ...UIOnlyParams })) {
      // parameters not in the view model are REST parameters, with no intention of being shown in the UI
      const inViewModel = toolUIParamModel?.[key] !== undefined;
      const submissionName = get(toolUIParamModel, [key, "submissionName"]);
      const isVisible = inViewModel && get(toolUIParamModel, [key, "visible"], true);
      // parameters without a set "visible" property are assumed to be visible, so we default to true

      // If the key has a submissionName and is visible, rename the key
      if (submissionName !== undefined && isVisible === true) {
        const newKey = useSubmissionName === true ? submissionName : key;
        const removedKey = useSubmissionName === true ? key : submissionName;

        renamedParams[newKey] = value;
        delete updatedParams[removedKey];
      } else if (inViewModel === false && key !== "context") {
        // TODO: Remove the hardcoded check for "context" and improve logic to determine if a parameter
        // should be removed or not. "context" is an example of a REST parameter that is not in the
        // view model but should not be removed.

        // If the key is not in the view model at all, remove it
        // These are REST parameters that were not visible in the UI
        delete updatedParams[key];
      }
    }

    // Add the renamed parameters to the updated parameters
    Object.assign(updatedParams, renamedParams);
  }

  return updatedParams;
}

/**
 * Returns the job parameters replaced by the UI only parameters with submission names
 * @param {AnalysisToolData} jobParams the job parameters to be replaced
 * @param {AnalysisToolData} UIOnlyParams the UI only parameters to replace with
 * @param {string} toolName the name of the tool to determine if renaming is needed
 * @returns {AnalysisToolData} the prepared/updated job parameters
 */
export function filterAndRenameJobParams(
  jobParams: AnalysisToolData | undefined,
  UIOnlyParams: AnalysisToolData | undefined,
  toolName: string | undefined
): AnalysisToolData {
  let preppedJobParams = jobParams ?? {};

  if (toolName === "ChooseBestFacilities") {
    const UIparameterViewModel = UIOnlyParams?.[ParamViewModelKey];
    preppedJobParams = filterAndRenameParams({
      jobParams,
      UIOnlyParams,
      toolUIParamModel: UIparameterViewModel,
      useSubmissionName: false
    });
  }

  // For Batch Geocode tool we only want to show the `geocodeUIParameters` that match UI key value table.
  if (toolName === "BatchGeocode") {
    preppedJobParams = omit(preppedJobParams, "geocodeParameters");
  }

  return preppedJobParams;
}

/**
 *
 * @param toolData
 * @param jobParams
 * @param outParamName
 */
export function serializeOutputName(
  toolData: AnalysisToolData,
  jobParams: AnalysisToolData,
  outParamName: string
): string {
  const outputName: OutputName = {};
  if (
    toolData !== undefined &&
    (toolData.resultType === undefined ||
      (toolData.resultType !== undefined &&
        [
          "TILED_IMAGERY_LAYER",
          "DYNAMIC_IMAGERY_LAYER",
          "FEATURE_LAYER",
          AnalysisSupportedItemTypes.FeatureLayer,
          AnalysisSupportedItemTypes.FeatureService,
          AnalysisSupportedItemTypes.ImageService
        ].includes(toolData.resultType as string)))
  ) {
    outputName.serviceProperties = {
      name: jobParams[outParamName] as string
    };
  }
  if (
    outputName.serviceProperties !== undefined &&
    toolData.resultType !== undefined &&
    toolData.resultType !== null &&
    ["TILED_IMAGERY_LAYER", "DYNAMIC_IMAGERY_LAYER"].includes(toolData.resultType as string)
  ) {
    outputName.serviceProperties.capabilities =
      toolData.resultType === "TILED_IMAGERY_LAYER" ? "Image, TilesOnly" : "Image";
  }
  if (toolData.itemProperties !== undefined) {
    outputName.itemProperties = toolData.itemProperties as OutputName["itemProperties"];
    if (outputName.itemProperties !== undefined) {
      outputName.itemProperties.title = jobParams[outParamName] as string;
      outputName.itemProperties.folderId = toolData.saveResultIn as string;
    }
  }
  return JSON.stringify(outputName);
}

function serializeGenericParameter(
  paramConfig: AnalysisToolUIParam,
  parameterObject: AnalysisToolDataItem
): AnalysisToolDataItem | undefined {
  let fullyProcessedValue;
  // Create a new serializer based on the serializationType
  const serializationType = paramConfig.serializationType;
  if (serializationType !== undefined) {
    const serializer = new serializers[serializationType]();

    // Perform the serialization
    const preparedData = serializer.prepare(parameterObject, paramConfig);
    const serializedValue = serializer.serialize(preparedData);
    fullyProcessedValue = serializer.postProcess(serializedValue, paramConfig) as AnalysisToolDataItem;
  }
  return fullyProcessedValue;
}

/**
 * Perform "one way" serialization of the job params for the job submission. The returned object is not intended to be
 * deserialized
 *
 * The function checks if one or more job parameter has serialization properties as made available in AnalysisToolUIParam.
 * One or more JobParameters can have serializationType, serializationParameterName, serializationKeys defined to
 * determine its serialization. See AnalysisToolUIParam for documentation of these properties.
 *
 * We can have these scenarios for a jobParameter object when one or more of the above properties are defined.
 * serializationType alone can be defined
 * serializationParameterName alone cannot be used it needs the serializationType to be defined as well to do serialization based on the type provided.
 * serializationKeys alone cannot be defined it needs serializationParameterName to be defined as well, so we can serialize the keys listed to this
 * serialization parameter name provided. This agreement helps us to have intent declared explicitly.
 *
 * Case A: serializationType alone can be defined
 * horizontalFactor : { type: "planar", startAngle: -90, endAngle: 90}
 * would be serialized to
 * => horizontalFactor: "planar -90 90"
 *
 * Case B: serializationParameterName is defined
 * Sub-Case B.1: serializationParameterName is defined and serializationKeys is undefined
 * If one of its property is used as serializationParameterName
 * it would set the value to that property.
 * recordsToMatch: {"orderByFields": "field ", sortOrder: "ASC"}
 * serializationParameterName is orderByFields
 * would be serialized to
 * => recordsToMatch: {"orderByFields": "field ASC"}
 * Sub-Case B.2 serializationParameterName is defined and serializationKeys and serializationKeys is defined.
 * serializationKeys is ["orderByFields", "sortOrder"]
 * recordsToMatch: { "orderByFields": "field ", sortOrder: "ASC", topOrder: 1}
 * serializationParameterName is orderByFields
 * => recordsToMatch: {"orderByFields": "field ASC", topOrder: 1}
 *
 * Case C: if the parameterObject does not have serializationParameterName property name it would  be create
 * and store the serialized value
 * Sub-Case C.1: serializationKeys is undefined
 * serializationParameterName: orderGrouping
 * recordsToMatch: {"orderByFields": "field ", sortOrder: "ASC"}
 * serializationKeys is undefined
 * => recordsToMatch: {"orderGrouping": "field ASC"}
 * Sub-Case C.2 When serializationKeys is defined
 * serializationKeys is ["orderByFields", "sortOrder"]
 * serializationParameterName: orderGrouping
 * and recordsToMatch: { "orderByFields": "field ", sortOrder: "ASC", topOrder: 1}
 * => recordsToMatch: {"orderGrouping": "field ASC", topOrder: 1}
 * @param {PrepJobParamsForSubmissionOptions} options - the options for the function
 * @param {AnalysisToolData} options.jobParams - the job parameters to be serialized
 * @param {AnalysisToolData} options.toolData - unprocessed tool data, used to support renamed parameters
 * @param {toolUIJson} options.toolUIJson - the tool UI json which contains the serialization properties
 * @param {toolJSON} options.toolJSON - the json for the tool, this is used for output name serialization.
 * @param {string} options.toolName - the name of the tool to determine if renaming is needed
 * @param {boolean} options.isPortal - a flag to determine if the portal is on premise or not
 * @returns {AnalysisToolData} - the serialized job parameters
 */
export function prepJobParamsForSubmission(options: PrepJobParamsForSubmissionOptions): AnalysisToolData {
  const { jobParams, toolData, toolUIJson, toolName, toolJSON, isPortal = false } = options;
  // loop through job params and check if toolJson has a param with a serializationType set
  let serializedJobParams: AnalysisToolData = { ...jobParams };
  const paramsByName = getUIParamsByName(toolUIJson);

  const parameterNames = toolJSON?.parameters
    ?.filter((parameter: AnalysisToolParam) => parameter.direction !== AnalysisToolParamDirection.Output)
    ?.map((parameter: AnalysisToolParam) => parameter.name);
  const actualOutputNames = getActualOutputNames(parameterNames);

  if (toolName === "BatchGeocode" && toolData !== undefined) {
    // translate the UI only parameter `geocodeUIParameters` to the schema that `BatchGeocode` expects.
    const geocodeUIParameters = toolData.geocodeUIParameters as ProcessedDataProperties;
    const geocodeParameters = toolData.geocodeParameters as AnalyzeGeocodeResult;
    const singleLine = toolData.singleOrMultiFieldSwitch === "singleField";
    const addressFields = paramsByName.geocodeUIParameters.UIparameterInfos!.map(
      (addressField: AnalysisUIParamProps) => addressField.name!
    );
    let fieldMapping: string[][];
    if (singleLine) {
      fieldMapping = addressFields.reduce((acc: string[][], curr) => {
        if (curr === "SingleLine") {
          acc.push([geocodeUIParameters[curr] ?? "", curr]);
        } else {
          acc.push([geocodeUIParameters[curr] ?? "", ""]);
        }
        return acc;
      }, []);
    } else {
      fieldMapping = addressFields.reduce((acc: string[][], curr) => {
        if (curr !== "SingleLine") {
          acc.push([geocodeUIParameters[curr] ?? "", curr]);
        }
        return acc;
      }, []);
    }
    geocodeParameters.field_mapping = JSON.stringify(fieldMapping);
    serializedJobParams.geocodeParameters = geocodeParameters;
    if (!isEmptyDataItem(serializedJobParams.inputFileItem)) {
      // `BatchGeocode` expects `itemid` with lowercase `i`
      serializedJobParams.inputFileItem = { itemid: (<ItemId>serializedJobParams.inputFileItem)?.itemId };
    }
  }

  for (const paramName in serializedJobParams) {
    const paramConfig = paramsByName[paramName];
    const parameterValue = serializedJobParams[paramName];

    // if parameter is not visible it would be already have its value undefined from analysis-tool
    // if parameter is visible but has "empty" values like "", [], {}, undefined, null, {prop1: undefined, prop2: undefined}
    // serialize it as undefined so it is not passed in job submission to the analysis tool.
    if (isEmptyDataItem(parameterValue)) {
      serializedJobParams[paramName] = undefined;
    } else if (typeof parameterValue === "object" && paramConfig?.serializationType !== undefined) {
      serializedJobParams[paramName] = serializeGenericParameter(paramConfig, parameterValue);
    } else if (typeof parameterValue === "string" && actualOutputNames.includes(paramName) && toolData !== undefined) {
      const outputNameObjectString = serializeOutputName(toolData, jobParams, paramName);
      // FIXME: We should combine/refactor the formatOutputNameForPortal and serializeOutputName functions
      // to avoid re-parsing the object created in serializeOutputName in formatOutputNameForPortal.
      let formattedOutputName = outputNameObjectString;
      try {
        formattedOutputName = formatOutputNameForPortal(outputNameObjectString, isPortal);
      } catch (error) {
        // TODO: We incorrectly thought the parameterValue is an output name, but it is not.
        // For now we will just ignore the error and pass the parameterValue as is.
      }
      serializedJobParams[paramName] = formattedOutputName;
    }
  }

  // Check if a key in toolData should be renamed to a key in jobParams
  // TODO: look for submissionName usage in the parameter view model to
  // determine if we should call this rather than relying on the toolName
  if (toolName === "ChooseBestFacilities") {
    const UIparameterViewModel = toolData?.[ParamViewModelKey];
    const UIOnlyParams = omit(toolData, Object.keys(serializedJobParams));

    serializedJobParams = filterAndRenameParams({
      jobParams: serializedJobParams,
      UIOnlyParams,
      toolUIParamModel: UIparameterViewModel
    });
  }

  return omitBy(serializedJobParams, isEmptyDataItem) as AnalysisToolData;
}

/**
 * Removes tokens from jobParams
 * @param {AnalysisToolData} jobParams the item to sanitize
 * @returns {AnalysisToolData} the same jobParams as input, only sanitized.
 */
export function sanitizeTokensFromJobParams(jobParams: AnalysisToolData): AnalysisToolData {
  for (const key of Object.keys(jobParams)) {
    let currentParam = jobParams[key];
    if (currentParam !== undefined && currentParam !== null) {
      // FIXME: use better detection for what is a URL.
      if (typeof currentParam === "string" && currentParam.includes("http")) {
        currentParam = sanitizeUrl(currentParam);
      } else if (Array.isArray(currentParam)) {
        currentParam.forEach((param: any) => {
          if (typeof param === "object" && "url" in param && param.url !== undefined && param.url !== null) {
            param.url = sanitizeUrl(param.url as string);
            param.serviceToken = undefined;
          }
        });
      } else if (
        typeof currentParam === "object" &&
        "url" in currentParam &&
        currentParam.url !== undefined &&
        currentParam.url !== null
      ) {
        currentParam.url = sanitizeUrl(currentParam.url as string);
        (currentParam as any).serviceToken = undefined;
      } else if (key === "context" && typeof currentParam === "object") {
        sanitizeTokensFromJobParams(currentParam as AnalysisToolData);
      }
    }
    jobParams[key] = currentParam;
  }

  return jobParams;
}
