import IdentityManager from "@arcgis/core/identity/IdentityManager";
import Portal from "@arcgis/core/portal/Portal";
import PortalUser from "@arcgis/core/portal/PortalUser";
import esriId from "@arcgis/core/identity/IdentityManager";

import memoize from "fast-memoize";

import { ErrorKeywords } from "../errors/interfaces";
import { throwError } from "../t9n/errors";
import { isEmptyDataItem, request } from "../common";
import { UserLicensedItem } from "../schema/interfaces";
import { getFederatedServers } from "../imagery-layer";
import { FederatedServer, FederatedServerLicenseInfo, ServerEditionName } from "../interfaces";

/**
 * Checks if the user is an admin
 * @param user portal's user instance
 * @returns
 */
function isAdmin(user: PortalUser): boolean {
  return user.role?.includes("admin") ?? false;
}

/**
 * Checks if the user is a publisher
 * @param user portal's user instance
 * @returns
 */
function isPublisher(user: PortalUser): boolean {
  return user.role?.includes("publisher") ?? false;
}

/**
 * Determine if user has access to analysis run spatial analysis jobs.
 * @param user
 * @returns
 */
export function checkUserAccess(user: PortalUser): boolean {
  let hasAccess = true;

  // let errorMessage = "";
  if (isEmptyDataItem(user?.orgId) === true) {
    // analysis only available for org level users.
    hasAccess = false;
    // TODO: errorMessage = i18n.orgUsrMsg;
  } else {
    hasAccess = isAdmin(user) || isPublisher(user);
    // TODO: errorMessage = i18n.pubRoleMsg;
  }
  return hasAccess;
}

/**
 * Checks if the user can perform standard analysis
 * @param portal portal instance
 * @returns
 */
export function canPerformStandardAnalysis(portal: Portal): boolean {
  const isServerAvailable = isEmptyDataItem(portal.helperServices?.analysis?.url) === false;
  const { user } = portal;
  const atLeastPublisher = isPublisher(user) || isAdmin(user);
  const privileges: string[] = user.sourceJSON.privileges ?? [];
  const hasAnalysisPrivilege =
    atLeastPublisher &&
    privileges.includes("portal:publisher:publishFeatures") &&
    privileges.includes("premium:user:spatialanalysis");
  return isServerAvailable && hasAnalysisPrivilege;
}

/**
 * Checks if the user can perform geo analysis
 * @param portal portal instance
 * @returns
 */
export function canPerformGeoAnalytics(portal: Portal): boolean {
  const isServerAvailable = isEmptyDataItem(portal.helperServices?.geoanalytics?.url) === false;
  const { user } = portal;
  const atLeastPublisher = isPublisher(user) || isAdmin(user);
  const privileges: string[] = user.sourceJSON.privileges ?? [];
  const hasGeoAnalyticsPrivilege =
    atLeastPublisher &&
    privileges.includes("portal:publisher:publishFeatures") &&
    privileges.includes("premium:publisher:geoanalytics");
  return isServerAvailable && hasGeoAnalyticsPrivilege;
}

/**
 * Fetches a user's licenses
 * @param portal portal instance
 * @returns
 */
export const fetchUserLicenses = memoize(
  async (portal: Portal) => {
    const { url: portalUrl } = portal;
    const url = `${portalUrl}/sharing/rest/community/self`;
    const credential = esriId.findCredential(url);

    let token;
    if (credential !== undefined) {
      token = credential.token;
    }

    const options = {
      query: {
        returnUserLicensedItems: true,
        token: token,
        f: "json"
      }
    };

    try {
      const {
        data: { userLicensedItems }
      } = await request(url, options);
      return [...userLicensedItems.map(({ entitlements }: UserLicensedItem) => entitlements)].flat();
    } catch (e) {
      await throwError(ErrorKeywords.NetworkError);
    }

    return [];
  },
  {
    serializer: (args) => `${(args as unknown as Portal).user.username}`
    // serializer: (args) => `${(args as unknown as Portal).user.id}`
  }
);

/**
 * Checks a list of a user's entitlements to see if that user has image entitlemenet
 * @param portal portal instance
 * @returns
 */
export async function hasImageEntitlement(portal: Portal): Promise<boolean> {
  let userHasImageEntitlement;

  try {
    const entitlements = await fetchUserLicenses(portal);
    userHasImageEntitlement = entitlements?.some((entitlement: string) => entitlement.toLowerCase() === "image");
  } catch (e) {
    userHasImageEntitlement = false;
  }

  return userHasImageEntitlement;
}

/**
 * Checks if the user can perform raster analysis
 * @param {Portal} portal portal instance
 * @returns {Promise<boolean>} true if the user can perform raster analysis
 */
export async function canPerformRasterAnalysis(portal: Portal): Promise<boolean> {
  const isServerAvailable = isEmptyDataItem(portal.helperServices?.rasterAnalytics?.url) === false;
  const { user } = portal;
  const atLeastPublisher = isPublisher(user) || isAdmin(user);
  const privileges: string[] = user.sourceJSON.privileges ?? [];
  // The entitlement check is not needed for Enterprise.
  // The entitlement is referring to the ArcGIS Image for ArcGIS Online extension license assignment.
  const userHasImageEntitlement = portal.isPortal ? true : await hasImageEntitlement(portal);
  const hasRasterAnalyticsPrivilege: boolean =
    atLeastPublisher &&
    userHasImageEntitlement &&
    privileges.includes("portal:publisher:publishFeatures") &&
    privileges.includes("premium:publisher:rasteranalysis") &&
    (privileges.includes("portal:publisher:publishDynamicImagery") ||
      privileges.includes("portal:publisher:publishTiledImagery"));

  let hasRasterAnalyticsServer = true;
  if (portal.isPortal) {
    // for ArcGIS Enterprise deployed on WL or K8s, look for RA serverFunction in FederatedServers
    try {
      hasRasterAnalyticsServer = (await getFederatedServers(portal))?.some((server) =>
        server?.serverFunction?.toLowerCase()?.includes("rasteranalytics")
      );
    } catch (e) {
      // in case of Network Error, reset hasRasterAnalyticsServer back to true as default.
      // the helperServices check should correctly determine if Enterprise has RA server or not.
      hasRasterAnalyticsServer = true;
    }
  }

  return isServerAvailable && hasRasterAnalyticsPrivilege && hasRasterAnalyticsServer;
}

/**
 * Checks if user had minimum privileges to use analysis widget in MV
 * @param {Portal} portal portal instance
 * @returns {boolean} true if user has minimum privileges
 */
export async function canUseAnalysis(portal: Portal): Promise<boolean> {
  return (
    checkUserAccess(portal?.user) && (canPerformStandardAnalysis(portal) || (await canPerformRasterAnalysis(portal)))
  );
}

/**
 * Finds the credentials for a portal, wrapper for JSAPI method.
 * @param {string} url the url of the portal
 * @param {string} userId the user id of the portal
 * @returns {__esri.Credential} the associated credential
 */
export function findCredential(url: string, userId: string): __esri.Credential {
  return IdentityManager.findCredential(url, userId);
}

/**
 * function to get deep learning query url
 * @param {Portal} portal portal instance
 * @returns {string} url string
 */
export function getDeepLearningQueryUrl(portal: Portal) {
  const { helperServices } = portal;
  return `${helperServices.rasterAnalytics.url}/QueryDeepLearningModelInfo`;
}

/**
 * checks if standard(feature) analysis server is advanced, this is
 * used in enterprise if the server license is advanced to enable
 * certain tools and tool parameter
 * @param {Portal} portal instance of the portal
 * @returns {Promise<boolean>}
 */
export async function isStandardAnalysisServerAdvanced(portal: Portal): Promise<boolean> {
  let hasAdvancedLevelLicense = false;
  const { id, restUrl } = portal;
  try {
    const federatedServers = await getFederatedServers(portal);
    const hostingServers = federatedServers.filter((server: FederatedServer) => server.serverRole === "HOSTING_SERVER");
    // currently there is only one HOSTING_SERVER , in future could be more , then account for each to get the license
    // Standard Feature Analytics is only based on one HOSTING_SERVER would need more information in that scenario to determine
    // Analysis Server at that time.Using helperservices to check this.
    const hostingServer = hostingServers?.[0] ?? undefined;
    if (hostingServer !== undefined) {
      const url = `${restUrl}/portals/${id}/servers/${hostingServer.id}`;
      const credential = esriId.findCredential(url);
      let token;
      if (credential !== undefined) {
        token = credential.token;
      }

      const options = {
        query: {
          token: token,
          f: "json"
        }
      };
      const serverResponse = (await request(url, options)) as { data: FederatedServerLicenseInfo };
      const serverInfo = serverResponse?.data;
      // when edition and level are undefined for hosting server we default to advanced server license.
      // Example: Kubernetes does not return these properties.
      const defaultLicenseLevel =
        isEmptyDataItem(serverInfo?.edition) && isEmptyDataItem(serverInfo?.level) ? ServerEditionName.Advanced : null;
      const licenseLevel = serverInfo?.edition?.name ?? defaultLicenseLevel;
      hasAdvancedLevelLicense = licenseLevel === ServerEditionName.Advanced;
    }
  } catch (e) {
    //no-op
  }
  return hasAdvancedLevelLicense;
}
