import { formatMessage } from "../localization";

import { ProcessInfoMessage } from "./interfaces";

function replaceTag(tag: "&" | "<" | ">"): string {
  const tagsToReplace = {
    "&": "&amp;",
    "<": "&lt;",
    ">": "&gt;"
  };
  return tagsToReplace[tag] || tag;
}

function safeTagsReplace(str: string): string {
  /* checking minimum characters <, > at this time */
  return str.replace(/[<>]/g, replaceTag);
}

/**
 * Builds the process info report from the job messages
 * Port from 3.x: https://devtopia.esri.com/WebGIS/arcgis-js-api/blob/master/esri/dijit/analysis/utils.js#L276
 * // TODO: Fetch its own strings and not have as function argument
 * @param {string[]} messagesArray - array of messages to be de-serialized
 * @param {Record<string, string>} t9nMessageCodes - GP message codes to use for translation
 * @returns {string} the formatted html report
 */
export function buildProcessInfoReport(messagesArray: string[], t9nMessageCodes: Record<string, string> = {}): string {
  let formattedReport = "";
  messagesArray.forEach((report: string) => {
    // Parse the stringified object into the `ProcessInfoMessage` type object
    const messageObject: ProcessInfoMessage = JSON.parse(report);
    const { message, messageCode, params, style } = messageObject;
    if (Array.isArray(message)) {
      const substitutionKeys: Record<string, string> = {};
      let currentMessage = style;
      // Loop below will replace values in this ^ html string with translations and actual values
      message.forEach((singleMessage, index) => {
        // Generate a key to reference the t9n json and use for later substitution
        const messageCodeKey = `${messageCode}_${index}`;
        // Replace the first closing of the td tag with an index based template string
        currentMessage = currentMessage.replace("></", ">${" + messageCodeKey + "}</");
        // Get the translated message based on the code + index
        let localizedMessage =
          t9nMessageCodes[messageCodeKey] !== undefined ? t9nMessageCodes[messageCodeKey] : singleMessage;
        // Replace based on the params in the gp message
        localizedMessage = formatMessage(localizedMessage, params);
        localizedMessage = safeTagsReplace(localizedMessage);
        // Populate mapping object for key to replace and its substituted value using `formatMessage`
        substitutionKeys[messageCodeKey] = `${localizedMessage}`;
      });
      // Finally - substitute the templates created above and concat the string
      formattedReport += formatMessage(currentMessage, substitutionKeys);
    } else {
      let localizedMessage = t9nMessageCodes[messageCode] !== undefined ? t9nMessageCodes[messageCode] : message;
      localizedMessage = formatMessage(localizedMessage, params);
      const firstClosingTag = style.indexOf("</");
      // Constructs a string like `<u><b>Initial Data Assessment</b></u><br/>`
      formattedReport += `${style.substring(0, firstClosingTag)}${localizedMessage}${style.substring(firstClosingTag)}`;
    }
  });

  return formattedReport;
}
