import request from "@arcgis/core/request";
import Portal from "@arcgis/core/portal/Portal";
import * as geoprocessor from "@arcgis/core/rest/geoprocessor";

import memoize from "fast-memoize";

import {
  AnalysisEngine,
  AnalysisServiceInfo,
  AnalysisToolInfo,
  AnalysisToolParam,
  AnalysisToolUITravelModeInfo
} from "analysis-ui-schema";
import { ArcgisWebAnalysisError, ErrorKeywords } from "analysis-shared-utils";

/**
 * Returns the analysis server url based on the engine and portal.
 * @param engine
 * @param portal
 * @param isGeocode
 */
export function getAnalysisServer(engine: AnalysisEngine, portal: Portal, isGeocode = false): string {
  let analysisServer = portal.helperServices.analysis.url;
  switch (engine) {
    case AnalysisEngine.GeoAnalytics: {
      analysisServer = portal.helperServices.geoanalytics.url;
      break;
    }
    case AnalysisEngine.Raster: {
      analysisServer = portal.helperServices.rasterAnalytics.url;
      break;
    }
    case AnalysisEngine.Standard: {
      // geocode task is not located in the same folder as standard analysis services
      analysisServer = isGeocode ? portal.helperServices.asyncGeocode.url : portal.helperServices.analysis.url;
      break;
    }
    default: {
      break;
    }
  }
  return analysisServer;
}

const serializerFunction = (args: any[]) => {
  const modifiedArgs = Array.from(args).map((param) => {
    let modifiedParam;
    // param instanceof Portal fails when in apps like MV using AMD
    // version of JSAPI this instanceof check fails.
    // https://devtopia.esri.com/WebGIS/arcgis-web-analysis/issues/512#issuecomment-3479460
    if (typeof param === "object" && param.id !== undefined) {
      modifiedParam = param.id;
    } else {
      modifiedParam = param;
    }
    return modifiedParam;
  });
  return JSON.stringify(modifiedArgs);
};

export const getToolInfoJson = memoize(
  async (toolName: string, engine: AnalysisEngine, portal: Portal): Promise<AnalysisToolInfo> => {
    const analysisServer = getAnalysisServer(engine, portal, toolName === "BatchGeocode");
    // Analysis Servers are Federated Servers with ArcGIS Online/Enterprise
    // Server token is generated from portal taken for all Federated Servers
    // irrespective how the portal token is generated: OAuth, token based, Web-Tier based authentications
    const toolUrl = `${analysisServer}/${toolName}?f=json`;
    const response = await request(toolUrl, { responseType: "json", authMode: "no-prompt" });
    const toolJson: AnalysisToolInfo = await response.data;

    // change "defaultValue" properties set to null to undefined in order to prevent a null value
    // from overwriting an undefined value when applying defaults
    // If similar cases are found in the future, consider using a robust schema validation/conversion
    // instead. That way we can be sure that the data is in the correct format
    toolJson.parameters?.forEach((param) => {
      if (param.defaultValue === null) {
        param.defaultValue = undefined;
      }
    });

    return toolJson;
  },
  {
    serializer: serializerFunction
  }
);

export const getToolInfoJsonsByEngine = memoize(
  async (engine: AnalysisEngine, portal: Portal): Promise<AnalysisToolInfo[]> => {
    const analysisServer = getAnalysisServer(engine, portal);
    const response = await request(`${analysisServer}?f=json`, { responseType: "json" });
    const {
      data: { tasks }
    } = response;
    const toolPromises = tasks.map((toolName: string) => getToolInfoJson(toolName, engine, portal));
    const toolResponses: AnalysisToolInfo[] = await Promise.all(toolPromises);
    return toolResponses;
  },
  {
    serializer: serializerFunction
  }
);

/*
 * Returns all the Analysis Tool JSONs from the Analysis server based on the analysis engine.
 *
 */
export const getAllToolInfoJsons = memoize(
  async (portal: Portal, engine?: AnalysisEngine): Promise<Map<string, AnalysisToolInfo>> => {
    // This function is memoized to return the toolJsons Map from cache.
    let toolResponses: AnalysisToolInfo[] = [];
    if (engine !== undefined) {
      toolResponses = await getToolInfoJsonsByEngine(engine, portal);
    } else {
      // get all the tools from all engines when engine is undefined.
      toolResponses = [
        ...(await getToolInfoJsonsByEngine(AnalysisEngine.Standard, portal)),
        ...(await getToolInfoJsonsByEngine(AnalysisEngine.Raster, portal)),
        ...(await getToolInfoJsonsByEngine(AnalysisEngine.GeoAnalytics, portal))
      ];
    }
    // creating a map with key as tool name in lower-case.
    // tool names come in PascalCase from Analysis Servers.
    return new Map(toolResponses.map((toolJson) => [toolJson.name.toLowerCase(), toolJson]));
  },
  {
    serializer: serializerFunction
  }
);

/**
 * Returns the travelModes available on an Organization
 */
export const getTravelModes = memoize(
  async (portal: Portal): Promise<AnalysisToolUITravelModeInfo | undefined> => {
    const { helperServices } = portal;
    let travelModeInfo: AnalysisToolUITravelModeInfo | undefined;
    if (helperServices.routingUtilities !== undefined) {
      const response = await geoprocessor.execute(`${helperServices.routingUtilities.url}/GetTravelModes?f=json`);
      const {
        results: [
          {
            value: { features }
          },
          { value: defaultTravelModeId }
        ]
      } = response;

      const travelModes = features.map((feature: any) => JSON.parse(feature.attributes.TravelMode));
      travelModeInfo = {
        travelModes,
        defaultTravelModeId
      } as AnalysisToolUITravelModeInfo;
    }
    return travelModeInfo;
  },
  {
    serializer: serializerFunction
  }
);

export const getServiceInfoByUrl = memoize(async (serviceUrl: string): Promise<AnalysisServiceInfo> => {
  const response = await request(`${serviceUrl}?f=json`, { responseType: "json" });
  return response.data;
});

/*
 * Returns tool info json for a tool name in a GP Server. Used for web tools only
 */
export const getToolInfoJsonByUrl = memoize(async (serverUrl: string, toolName: string): Promise<AnalysisToolInfo> => {
  const toolUrl = `${serverUrl}/${toolName}?f=json`;
  const response = await request(toolUrl, { responseType: "json", authMode: "no-prompt" });
  const toolJson: AnalysisToolInfo = await response?.data;

  // change "defaultValue" properties set to null to undefined in order to prevent a null value
  // from overwriting an undefined value when applying defaults
  // If similar cases are found in the future, consider using a robust schema validation/conversion
  // instead. That way we can be sure that the data is in the correct format
  if (toolJson?.parameters !== undefined) {
    toolJson.parameters?.forEach((param: AnalysisToolParam) => {
      if (param.defaultValue === null) {
        param.defaultValue = undefined;
      }
    });
  }
  return toolJson;
});
/*
 * Returns all the Web Tool JSONs from the GP server based on the GP server url.
 *
 */
export const getToolInfoJsonsByUrl = memoize(async (serverUrl: string): Promise<AnalysisToolInfo[]> => {
  const response = await request(`${serverUrl}?f=json`, { responseType: "json" });
  const {
    data: { tasks }
  } = response;
  const toolPromises = tasks.map((toolName: string) => getToolInfoJsonByUrl(serverUrl, toolName));
  const toolResponses: AnalysisToolInfo[] = await Promise.all(toolPromises);
  return toolResponses;
});

/*
 * Returns complete tool information for a web tool
 */
export const getWebToolInfo = memoize(
  async (serverUrl: string, toolName: string): Promise<AnalysisToolInfo | undefined> => {
    // TODO: Add a try/catch that throws an error, part of issue #4975
    let toolJson: AnalysisToolInfo | undefined;
    let serviceInfoJson: AnalysisServiceInfo | undefined;
    try {
      toolJson = await getToolInfoJsonByUrl(serverUrl, toolName);
      serviceInfoJson = await getServiceInfoByUrl(serverUrl);
    } catch (exception) {
      throw new ArcgisWebAnalysisError(ErrorKeywords.WebToolFailedFetch, "WebTool JSON failed to fetch.");
    }
    toolJson.serviceInfo = serviceInfoJson;

    return { ...toolJson, ...{ serviceInfo: serviceInfoJson } };
  }
);
