import esriRequest from "@arcgis/core/request";

import { assign, isArray, isEmpty, isPlainObject, mapValues, omitBy, pickBy, toPath } from "lodash-es";

import {
  AnalysisToolDataItem,
  AnalysisToolPrimitiveDataItem,
  AnalysisValidationParams,
  MessageType
} from "analysis-ui-schema";

import { NestedObject } from "../spatial-reference/spatial-references";
import { formatMessage } from "../localization";
import { UIInputStatus } from "../inputs/interfaces";

/**
 * Indicates if a provided number value is valid.
 * An invalid value is: undefined | NaN | Infinite | -Infinite | null
 * See for more details:
 *   - https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Number/isFinite
 *   - https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Number/isNaN
 * @param {number | undefined | null} value value to check
 * @returns {boolean} if valid or not
 */
export function isValidNumber(value: number | undefined | null): boolean {
  return value !== undefined && value !== null && Number.isNaN(value) === false && Number.isFinite(value) === true;
}

/**
 * Zips together two arrays of the same length, similar to
 * Python's zip() function.
 * See for more details:
 *    - https://stackoverflow.com/questions/22015684/how-do-i-zip-two-arrays-in-javascript/22015930
 * @param array1
 * @param array2
 */
export function zip<T, K>(array1: T[], array2: K[]): (T | K)[][] {
  return array1.map((a, index) => [a, array2[index]]);
}

/**
 * Checks for duplicate strings in an array. Returns true if there
 * are duplicates, false if each string is unique.
 * @param arr
 */
export function areThereDuplicates(arr: string[]): boolean {
  return new Set(arr).size !== arr.length;
}

/**
 * Removes properties with empty objects from an object
 * @param obj
 * @returns
 */
export function removeEmptyObjects<T extends object | null | undefined>(obj: T): T {
  // Based on https://stackoverflow.com/questions/38275753/how-to-remove-empty-values-from-object-using-lodash solution.
  let nonEmptyObjects;
  nonEmptyObjects = pickBy(obj, isPlainObject); // pick only objects
  nonEmptyObjects = mapValues(nonEmptyObjects, removeEmptyObjects); // recursive call for nested objects
  nonEmptyObjects = omitBy(nonEmptyObjects, isEmpty); // remove all empty objects
  nonEmptyObjects = assign(omitBy(obj, isPlainObject), nonEmptyObjects); // assign back primitive values
  return nonEmptyObjects as T;
}

function isEveryPropertyEmpty(value: AnalysisToolDataItem): boolean {
  let isAllPropsEmpty = true;
  if (typeof value === "object" && isArray(value) === false) {
    // using for-in as it will work with objects with properties that set via accessors as well
    for (const prop in value) {
      // This will tell the TypeScript compiler that the obj object has a string index signature,
      // and will allow to access its properties using a string index without getting the "Element implicitly has an 'any' type" error.
      const propValue = (value as { [key: string]: AnalysisToolPrimitiveDataItem })?.[prop];
      if (value.hasOwnProperty(prop) === true && propValue !== undefined && propValue !== null && propValue !== "") {
        isAllPropsEmpty = false;
        break;
      }
    }
  }
  return isAllPropsEmpty;
}

/**
 * Check for an empty value for data items like GPString and Field.
 * @param value data item value.
 * @returns whether the value is considered empty.
 */
export function isEmptyDataItem<T>(value: AnalysisToolDataItem | T): boolean {
  return (
    value === undefined ||
    value === null ||
    // single-select, "" is a special case for strings
    (typeof value === "string" && value === "") ||
    // single-select, empty objects are a special case for nested values
    (isPlainObject(value) && isEmpty(value)) ||
    // multi-select
    (Array.isArray(value) && value.length < 1) ||
    // only testing objects which are not arrays
    // TODO: combine isEveryPropertyEmpty and isEmptyDataItem to single function
    (typeof value === "object" && isArray(value) === false && isEveryPropertyEmpty(value))
  );
}

/**
 * Checks whether or not a localName is included in an event's composedPath
 * This is very useful for popover patterns.
 * @param composedPath the event.composedPath() from the event
 * @param elementName the localName that you want to see if it is included
 * @returns boolean: whether or not the localName is in the composedPath
 */
export function isInComposedPath(composedPath: EventTarget[], elementName: string): boolean {
  const localNames = composedPath.map((target: EventTarget) => {
    return (target as HTMLElement).localName;
  });
  return localNames.includes(elementName);
}

/**
 * Wrapper for the esriRequest
 * @param url the url
 * @param options the response type and other options
 * @returns RequestResponse
 */
export async function request(url: string, options: __esri.RequestOptions): Promise<__esri.RequestResponse> {
  return esriRequest(url, options);
}

/**
 * Flatten a nested object into a single level.
 * https://stackoverflow.com/a/55251598
 * @param {NestedObject} obj the object to flatten
 * @returns {Record<string, string>} the flattened object
 */
export const flattenObject = (obj: NestedObject): Record<string, string> => {
  const flattened: Record<string, string> = {};
  Object.keys(obj).forEach((key) => {
    const value = obj[key];
    if (typeof value === "object") {
      Object.assign(flattened, flattenObject(value));
    } else {
      flattened[key] = value;
    }
  });
  return flattened;
};

/**
 * This function immutably sets a value at a path in an object.
 * @param {Record<string, unknown>} obj The object to update
 * @param {string | string[]} path An array of strings representing the path to the value to update in the object
 * @param {unknown} value The new value to set at the specified path.
 * @returns {Record<string, unknown>} A new object with the updated value.
 * @example
 * immutablySet({ a: { b: { c: 1 } } }, ['a', 'b', 'c'], 2)
 * // returns { a: { b: { c: 2 } } }
 * @example
 * immutablySet({ a: { b: [1, 2, 3] } }, ['a', 'b', '2'], 4)
 * // returns { a: { b: [1, 2, 4] } }
 * @example
 * immutablySet({ a: { b: { c: 1 } } }, 'a.b.c', 2)
 * // returns { a: { b: { c: 2 } } }
 * @example
 * immutablySet({ a: { b: { c: 1 } } }, 'a.d', { e: 2 })
 * // returns { a: { b: { c: 1 }, d: { e: 2 } } }
 */
export function setImmutableValue(
  obj: Record<string, unknown>,
  path: string | string[],
  value: unknown
): Record<string, unknown> {
  const pathArray = Array.isArray(path) ? path : toPath(path);

  const newObj: Record<string, unknown> = { ...obj };
  let current: Record<string, unknown> | Record<string, unknown>[] = newObj;

  // Traverse the path, creating new shallow copies as needed
  for (let i = 0; i < pathArray.length - 1; i++) {
    const key = pathArray[i];

    // Check if the key exists or the next value is the wrong type
    const nextKeyIsNumber = isValidNumber(Number(pathArray[i + 1]));
    // This "overwrite" behavior is found in lodash.set
    const shouldResetValue = Array.isArray(current[key]) !== nextKeyIsNumber;
    if (current[key] === undefined || shouldResetValue) {
      current[key] = nextKeyIsNumber ? [] : {};
    }

    current[key] = nextKeyIsNumber
      ? [...(current[key] as Record<string, unknown>[])]
      : { ...(current[key] as Record<string, unknown>) };

    current = current[key] as Record<string, unknown>;
  }

  // Made it to the end of the path, set the value
  current[pathArray[pathArray.length - 1]] = value;

  return newObj;
}

/**
 * Parses a regex string and creates a RegExp object.
 * @param {string} regexString - The regex string in the format '/pattern/flags'.
 * @returns {RegExp|undefined} A RegExp object representing the parsed regex, or undefined if parsing fails.
 */
export function getRegExFromString(regexString: string): RegExp | undefined {
  let regexLiteral: RegExp | undefined;
  const regexParts = /^\/(.*?)\/([a-z]*)$/.exec(regexString);
  const pattern = regexParts?.[1];
  const flags = regexParts?.[2];
  if (!isEmptyDataItem(pattern)) {
    regexLiteral = new RegExp(pattern as string, flags);
  }
  return regexLiteral;
}

/**
 * Checks validationParamsMessage to make sure none of its fields are undefined before formatting message
 * @param {AnalysisValidationParams} validationParams
 * @returns {string|undefined} Formatted message with validationParams variables incorporated into the message
 */
export function formatValidationParamsMessage(validationParams?: AnalysisValidationParams): string | undefined {
  const { message, variables } = validationParams ?? {};
  // only message is required , we can have custom validation message without any variables
  const validationParamsMessage = message !== undefined ? formatMessage(message, variables ?? {}) : undefined;
  return validationParamsMessage;
}

/**
 * Converts an error key/code to a message template
 * @param {AnalysisValidationParams} validationParams
 * @param {{ [key: string]: string }} errorMessages
 * @returns {AnalysisValidationParams} validationParams with the message property (if a message exists for the error key)
 */
export function getValidationParamsMessageFromErrorKey(
  errorMessages: { [key: string]: string },
  validationParams?: AnalysisValidationParams
): AnalysisValidationParams {
  const { key, message: originalMessage, ...rest } = validationParams ?? {};
  const errorKey = key ?? "";
  // Check if the error key reference a validation param message or error message
  const message =
    errorMessages[errorKey] ?? validationParams?.[errorKey as keyof AnalysisValidationParams] ?? originalMessage;
  return { ...rest, message };
}

/**
 *
 * @param messageType
 */
export function getUIInputStatusFromMessageType(messageType?: MessageType) {
  const status = messageType === "informative" ? UIInputStatus.IDLE : UIInputStatus.INVALID;

  return status;
}

interface OpenableHtmlElement extends HTMLElement {
  open: boolean;
}

/**
 * @param {OpenableHtmlElement | undefined} element the element to close and remove
 */
export function removeOpenableDomElement(element: OpenableHtmlElement | undefined): void {
  if (element !== undefined) {
    element.open = false;
    element.remove();
  }
}

/**
 * @param {Document} document used to append
 * @param {OpenableHtmlElement} element the element to add to the dom and open
 */
export function addOpenableDomElement(document: Document, element: OpenableHtmlElement): void {
  if (document.body.contains(element) === false) {
    document.body.appendChild(element);
    element.open = true;
  }
}
