/**
 * Filters the messages for a historyItem to not include extraneous messages.
 * Part of the intended effect here is to reduce storage size.
 * @param {__esri.GPMessage[]} messages the messages to be filtered
 * @param {boolean = false} isWebtool whether or not the tool associated with the messages is a webtool, default to false.
 * @param analysisType
 * @returns {__esri.GPMessage[]} the filtered messages
 */
export function filterHistoryItemMessages(
  messages: __esri.GPMessage[],
  analysisType?: AnalysisType
): __esri.GPMessage[] {
  return messages.filter((message: __esri.GPMessage) => {
    let parsedMessage: { [key: string]: string };
    try {
      parsedMessage = JSON.parse(message.description);
    } catch (e) {
      parsedMessage = { description: message.description };
    }

    const isCreditMessage = "cost" in parsedMessage;
    const isTranslatableMessage = "messageCode" in parsedMessage;
    const isWebTool = analysisType === AnalysisType.WebTool;
    return (
      (message.type === "warning" || message.type === "error") &&
      (isCreditMessage || isTranslatableMessage || isWebTool)
    );
  });
}

/**
 * Converts the esriJobMessageType to something we use internally for comparisons.
 * @param {string} messageType the type of the message on the message object
 * @returns {string} the type used for internal comparisons
 */
export function convertEsriMessageType(messageType: string): string {
  let type = "";
  switch (messageType) {
    case "esriJobMessageTypeInformative":
      type = "informative";
      break;
    case "esriJobMessageTypeError":
      type = "error";
      break;
    case "esriJobMessageTypeWarning":
      type = "warning";
      break;
    case "error":
    case "informative":
    case "warning":
    default:
      type = messageType;
      break;
  }
  return type;
}

/**
 * Transforms GPFeatureRecordSetLayer from GPTask result params into mapLayers
 * @param {GPFeatureRecordSetLayer[]} jobLayers The result GPFeatureRecordSetLayer from a history item
 * @param {SupportedLayer[]} availableMapLayers the already available layers to check against
 * @returns {Promise<[SupportedLayer[], SupportedLayer[]]>} layers that should be added to mapLayers from the job params
 */
export async function convertJobLayersToMapLayers(
  jobLayers: GPFeatureRecordSetLayer[],
  availableMapLayers: SupportedLayer[]
): Promise<ConvertJobLayersToMapLayersReturnType> {
  const layersToAdd: GPFeatureRecordSetLayer[] = [];
  const existingLoadedLayers: SupportedLayer[] = [];
  const layerErrors: ArcgisWebAnalysisError[] = [];

  jobLayers.forEach((gpLayer: GPFeatureRecordSetLayer) => {
    let foundLayer;
    // Prefer using URL first over itemId
    if ("url" in gpLayer) {
      const findCb = (availableLayer: SupportedLayer) => {
        const hasLayerId = "layerId" in availableLayer;
        const urlId = hasLayerId ? `/${availableLayer.layerId ?? 0}` : "";
        const compareUrl = `${(availableLayer as FeatureLayer).url}${urlId}`;
        return sanitizeUrl(gpLayer.url) === compareUrl;
      };
      foundLayer = availableMapLayers.find((availableLayer) => findCb(availableLayer));
    } else if ("itemId" in gpLayer) {
      // TODO: Could be a feature collection if the job did not create a service
      // TODO: Does this handle sub layers?
      foundLayer = availableMapLayers.find((availableLayer) => gpLayer.itemId === availableLayer.portalItem?.id);
    } else if (
      isFeatureCollectionJSON(gpLayer as FeatureCollection) &&
      (gpLayer as FeatureCollection)?.layerDefinition.name !== undefined
    ) {
      foundLayer = availableMapLayers.find(
        (availableLayer) => (gpLayer as FeatureCollection)?.layerDefinition.name === availableLayer.title
      );
    }
    if (foundLayer === undefined) {
      layersToAdd.push(gpLayer);
    } else {
      existingLoadedLayers.push(foundLayer);
    }
  });

  const unloadedLayersToAdd = layersToAdd.map(async (layer: GPFeatureRecordSetLayer) => {
    let loadedLayer: Layer | undefined;
    // handle any errors in finding/creating the layer
    try {
      loadedLayer = await getInputLayerObject(layer);
    } catch (error) {
      layerErrors.push(
        new ArcgisWebAnalysisError(
          error.name,
          error.message,
          {
            ...error.details,
            layer
          },
          error.cause
        )
      );
    }

    // handle any errors in the layer loading
    try {
      loadedLayer = await loadLayer(loadedLayer as Layer);
    } catch (error) {
      layerErrors.push(
        new ArcgisWebAnalysisError(
          error.name,
          error.message,
          {
            ...error.details,
            layer
          },
          error.cause
        )
      );
    }

    return loadedLayer;
  });

  let loadedLayersToAdd = await Promise.all(unloadedLayersToAdd);
  loadedLayersToAdd = loadedLayersToAdd.filter((layer) => layer !== undefined) ?? [];
  // assertion to SupportedLayer[] is safe because of the filter for undefined above.
  return {
    existingLayers: existingLoadedLayers,
    layersToAdd: loadedLayersToAdd as SupportedLayer[],
    errors: layerErrors
  };
}

/**
 * This method takes in job params and tool ui json and converts it into a useable data object for a tool.
 * @param {object} props - The options object.
 * @param props.jobParams AnalysisToolData, typically from either a history item or from a GP Task
 * @param props.toolJSON Tool UI JSON for this specific tool
 * @param props.uiOnlyParams
 * @param props.availableMapLayers the layers in the map to check against
 * @param props.toolName The name of the current tool
 * @param props.isWebTool Whether or not the tool is a web tool
 * @returns {Promise<{convertedJobParams: AnalysisToolGlobalData, layers: SupportedLayer[]}>} Tool data
 */
export async function convertJobParamsToToolData(
  props: ConvertJobParamsToToolDataOptions
): Promise<ConvertJobParamsToToolDataReturnType> {
  const { jobParams, uiOnlyParams, toolName, availableMapLayers, toolJSON, isWebTool } = props;
  const convertedJobParams: AnalysisToolGlobalData = {};
  const resultingErrorState: AnalysisToolErrorState = {
    toolAppErrorState: [],
    parameterErrorState: {}
  };
  let layers: SupportedLayer[] = [];
  const toolUIParamModel = uiOnlyParams?.[ParamViewModelKey];
  let combinedParams = jobParams;
  /**
   * In case of `ChooseBestFacilities` where we use `submissionName` and different ui parameter
   * names for each REST parameter, we need the tool data to be updated based on the
   * combined parameters of jobParams and uiOnlyParams.
   * TODO: In fact we can use this approach for all tools but that can be risky for R2 as we
   * are past code freeze so scoped this combinedParams only for this tool.
   * All other tools iterate on jobParams as before
   */
  if (toolName === "ChooseBestFacilities") {
    combinedParams = { ...jobParams, ...uiOnlyParams };
  }

  for (const key in combinedParams) {
    const parameterValue = <any>combinedParams[key];
    /**
     * we are checking if submissionName was used for this key, this only applies for combinedParams of jobParams and uiParams
     * Only ChooseBestFacilities passes combinedParams in the above loop
     * using this name we find the `toolJSONParam`
     * for example: when key is "maxCapCov_demandLocationsLayer" parameterValue  = {
     *   "itemId": "82b3bc887ff842efa10e3ca24ab0b016"
     *   "url":""https://servicesdev.arcgis.com/b5ADkBof6gCHCFQm/ArcGIS/rest/services/Power_Plants_in_the_U_S/FeatureServer/0"
     * }
     * using parameterViewModel we get the submission name to be "demandLocationsLayer"
     * and we get the REST dataType as GPFeatureRecordSetLayer, which then sets the selectedLayer property for maxCapCov_demandLocationsLayer
     * correctly as maxCapCov_demandLocationsLayer_SelectedLayers = [layerInstance Object]
     * fixes https://devtopia.esri.com/WebGIS/arcgis-web-analysis/issues/3786
     */
    const submissionName = get(toolUIParamModel, [key, "submissionName"]);
    let toolJSONParam = toolJSON?.parameters?.find(
      (param: { name: string }) => param.name === key || param.name === submissionName
    );
    // If the ui only params contain a data type for the key, use that to determine the conversion of it's value
    // This is the case for the parameter-group as it's value is always sent as a GPString, but the data used to
    // re-populate the parameter-group (and within the component) can be of any type
    const dataTypeKey = generateParamGroupDataTypeKey(key);
    const dataType = uiOnlyParams[dataTypeKey] ?? toolJSONParam?.dataType;

    // If the tool is a web tool, we need to make sure that context is included in the converted
    // result. For web tools context is not set in the toolJson but can still be present in the
    // jobParams and should not be filtered out.
    const isWebToolContext = isWebTool && key === "context";
    if (isWebToolContext && toolJSONParam === undefined) {
      toolJSONParam = {
        name: "context",
        dataType: "GPString",
        direction: "esriGPParameterDirectionInput"
      };
    }

    if (parameterValue !== undefined && parameterValue !== null && toolJSONParam !== undefined) {
      switch (dataType) {
        case "GPString":
          if (key === "context") {
            convertedJobParams[key] = !isEmptyDataItem(parameterValue) ? parameterValue : undefined;
            continue;
          }
          if (!Array.isArray(parameterValue)) {
            if (typeof parameterValue === "object" && !Array.isArray(parameterValue)) {
              if (key === "model" || key === "modelArguments") {
                convertedJobParams[key] = parameterValue;
              } else {
                // Treat as a layer input
                const gpLayerValue = <GPFeatureRecordSetLayer>parameterValue;
                const {
                  existingLayers,
                  layersToAdd,
                  errors: layerErrors
                } = await convertJobLayersToMapLayers([gpLayerValue], availableMapLayers);
                const selectedLayersKey = generateSelectedLayersKey(key);
                if (layerErrors.length > 0) {
                  resultingErrorState.parameterErrorState[key] = getParameterErrors(layerErrors);
                }

                convertedJobParams[key] = gpLayerValue;
                convertedJobParams[selectedLayersKey] = [...existingLayers, ...layersToAdd];
                layers = layers.concat(layersToAdd);
              }
            } else {
              // Handle the output name parameter
              if (
                typeof parameterValue === "string" &&
                (parameterValue.includes("serviceProperties") || parameterValue.includes("itemProperties"))
              ) {
                const outputName: OutputName = JSON.parse(parameterValue);
                convertedJobParams[key] =
                  outputName?.serviceProperties?.name ?? outputName.itemProperties?.title ?? undefined;
              } else {
                // All other GPString can just be applied without extra logic
                convertedJobParams[key] = String(parameterValue);
              }
            }
            break;
          } else {
            // Else we do not break and fall through to the next condition
            // This way we cleanly handle the incorrect REST data type
            toolJSONParam.dataType = "GPMultiValue:GPString";
          }
        case "GPMultiValue:GPString":
          const multiParameterValue = Array.isArray(parameterValue) ? parameterValue : [parameterValue];
          if (multiParameterValue.every((val) => typeof val === "object")) {
            // Treat as a layer input
            const gpLayerValue = <GPFeatureRecordSetLayer[]>multiParameterValue;
            const {
              existingLayers,
              layersToAdd,
              errors: layerErrors
            } = await convertJobLayersToMapLayers(gpLayerValue, availableMapLayers);
            const selectedLayersKey = generateSelectedLayersKey(key);
            if (layerErrors.length > 0) {
              resultingErrorState.parameterErrorState[key] = getParameterErrors(layerErrors);
            }

            convertedJobParams[key] = gpLayerValue;
            convertedJobParams[selectedLayersKey] = [...existingLayers, ...layersToAdd];
            layers = layers.concat(layersToAdd);
          } else {
            convertedJobParams[key] = multiParameterValue.map((val) => String(val));
          }
          break;
        case "GPFeatureRecordSetLayer":
        case "GPRecordSet":
        case "GPMultiValue:GPFeatureRecordSetLayer":
        case "GPMultiValue:GPRecordSet":
          {
            const gpLayerValue = <GPFeatureRecordSetLayer | GPFeatureRecordSetLayer[]>parameterValue;
            const {
              existingLayers,
              layersToAdd,
              errors: layerErrors
            } = await convertJobLayersToMapLayers(
              Array.isArray(gpLayerValue) ? gpLayerValue : [gpLayerValue],
              availableMapLayers
            );
            const selectedLayersKey = generateSelectedLayersKey(key);
            if (layerErrors.length > 0) {
              resultingErrorState.parameterErrorState[key] = getParameterErrors(layerErrors);
            }

            convertedJobParams[key] = gpLayerValue;
            convertedJobParams[selectedLayersKey] = [...existingLayers, ...layersToAdd];
            layers = layers.concat(layersToAdd);
          }
          break;
        case "GPDate":
        case "GPBoolean":
        case "GPDouble":
        case "GPLong":
        case "GPMultiValue:GPDouble":
        case "GPLinearUnit":
        case "Field":
        default:
          convertedJobParams[key] = parameterValue;
          break;
      }
    }
  }
  return { convertedJobParams, layers, resultingErrorState };
}

/**
 * Parses the jobInfo specifically for a historyItem and returns the normal object instead of the serialized one.
 * @param {SerializedHistoryItem} serializedHistoryItem the serialized history item that needs to be parsed
 * @returns {AnalysisHistoryItem} the parsed history item
 */
export function parseSerializedHistoryItem(serializedHistoryItem?: SerializedHistoryItem): AnalysisHistoryItem {
  const serializedJobInfo =
    serializedHistoryItem?.jobInfo !== undefined ? JSON.parse(serializedHistoryItem?.jobInfo) : {};
  return {
    ...serializedHistoryItem,
    jobInfo: new JobInfo({ ...serializedJobInfo }),
    results:
      serializedHistoryItem?.results !== undefined
        ? serializedHistoryItem.results.map((result: SerializedParameterValue) => {
            const parsedResult = { ...result };
            try {
              parsedResult.value = Array.isArray(result.value)
                ? result.value.map((stringifiedResult: string) => JSON.parse(stringifiedResult))
                : JSON.parse(result.value);
            } catch (e) {
              // no-op
            }
            return new ParameterValue({
              dataType: parsedResult.dataType as ParameterValue["dataType"],
              paramName: parsedResult.paramName,
              value: parsedResult.value as ParameterValue["value"]
            });
          })
        : undefined
  } as AnalysisHistoryItem;
}

/**
 * Returns true if the message input is a credit message that is also a warning.
 * @param {__esri.GPMessage[]} message the message to be checked
 * @returns {boolean} true if the message is the credit message.
 */
export function isWarningCreditMessage(message: __esri.GPMessage): boolean {
  let isCreditMessage = false;
  try {
    isCreditMessage = "cost" in JSON.parse(message.description) && message.type === "warning";
  } catch {
    /** no-op */
  }
  return isCreditMessage;
}

/**
 * Get system raster function definition key from input RFT
 * @param {RFxTemplate} rfxTemplate to get the system raster function definition key from
 * @returns {string | undefined} the system RFx definition key or undefined for custom function cases
 */
export function getRFxDefKeyFromRFT(rfxTemplate: RFxTemplate): string | undefined {
  let functionDefKey = rfxTemplate?.function?.type;
  if (functionDefKey === "LocalFunction") {
    // for local funcitons, return rfx def key as `LocalFunction__${localOp}`
    // for example, return "LocalFunction__1" for Plus function
    const localOp = rfxTemplate.arguments?.Operation?.value?.toString();
    if (localOp !== undefined) {
      functionDefKey += `__${localOp}`;
    }
  }
  return functionDefKey;
}

/**
 * Get translated RFT name for the input history item
 * @param {AnalysisHistoryItem} historyItem to get translated RFT name for
 * @param {Map<string,string>} translatedRFTNamesMap contains all translated system RFT names
 * @returns {string} the translated RFT name
 */
export function getRFxTranslatedRFTName(
  historyItem: AnalysisHistoryItem,
  translatedRFTNamesMap: Map<string, string>
): string {
  const rfxTemplate = historyItem?.jobParams?.rasterFunction as RFxTemplate;
  const rftName = rfxTemplate?.name;
  let translatedRFTName;
  if (!isEmptyDataItem(rfxTemplate)) {
    if (rftName === rfxTemplate.function?.name) {
      const rfxDefKey = getRFxDefKeyFromRFT(rfxTemplate);
      if (rfxDefKey !== undefined) {
        translatedRFTName = translatedRFTNamesMap.get(rfxDefKey);
      }
    } else {
      // return the name of rfxTemplate for custom RFTs
      translatedRFTName = rftName;
    }
  }

  return translatedRFTName ?? rftName ?? "";
}

/**
 * Creates the history detail and adds to the DOM
 * @param {AnalysisHistoryItem} historyItem to parse tool name from
 * @param {LocaleItem} toolsT9nStrings strings to use
 * @returns {string} - translated tool name
 * @category Private
 */
export function getTranslatedToolName(historyItem: AnalysisHistoryItem, toolsT9nStrings: LocaleItem): string {
  const camelCaseToolName = `${historyItem.toolName.charAt(0).toLowerCase()}${historyItem.toolName.slice(1)}`;
  const key = historyItem.analysisEngine === AnalysisEngine.Raster ? `${camelCaseToolName}_ra` : camelCaseToolName;
  const translatedToolName = toolsT9nStrings[key] as string | undefined;
  return translatedToolName ?? historyItem.toolName;
}

/**
 * Returns true if the status inputted is cancellable and false otherwise
 * @param {AnalysisJobStatus} jobStatus the job status to check if it is cancellable
 * @returns {boolean} whether or not the status is cancellable.
 */
export function isStatusCancellable(jobStatus: AnalysisJobStatus): boolean {
  return (
    jobStatus === AnalysisJobStatus.Executing ||
    jobStatus === AnalysisJobStatus.Submitted ||
    jobStatus === AnalysisJobStatus.New ||
    jobStatus === AnalysisJobStatus.Waiting
  );
}

/**
 * Turns a feature collection into the version that we want to use and store in history and env settings.
 * should just be `{ layerDefinition: { name: string } }`
 * @param {FeatureCollection} value GP feature collection value
 * @returns {AnalysisToolDataItem} trimmed down feature collection for storage for history and env settings
 */
export function getFeatureCollectionValueForStorage(value: FeatureCollection): AnalysisToolDataItem | undefined {
  const trimmedFeatureCollection =
    value.layerDefinition.name !== undefined
      ? ({
          layerDefinition: {
            name: value.layerDefinition.name ?? ""
          },
          featureSet: {}
        } as AnalysisToolDataItem)
      : undefined;
  return trimmedFeatureCollection;
}
