import * as geoprocessor from "@arcgis/core/rest/geoprocessor";

import memoize from "fast-memoize";
import { cloneDeep, omit } from "lodash-es";

import { ModelParameters } from "analysis-ui-schema";

import {
  AnalyzeGeocodeResult,
  ArcgisWebAnalysisError,
  GetToolUIAddressFieldsParams,
  ProcessedDataProperties,
  getDeepLearningQueryUrl
} from "../index";
import { Dataset, FieldInfo } from "../tools/interfaces";

import { DimensionValues, DimensionVariables, ProcessedLayerProperties } from "./interfaces";
/**
 * @category Private
 * @param {__esri.ImageryLayer | __esri.ImageryTileLayer} layer - the imagery layer to be processed
 * @returns {ProcessedLayerProperties}
 * process multidimensional imagery layer to get dimensions and variables
 * also get rasterInfo from imagery layer for accessing properties
 */
export const processImageryLayer = memoize(
  async (layer: __esri.ImageryLayer | __esri.ImageryTileLayer): Promise<ProcessedLayerProperties> => {
    let processedProperties: ProcessedLayerProperties = { isMultidimensional: false };
    const mutlidimensionalInfo =
      layer.type === "imagery-tile" ? layer.rasterInfo.multidimensionalInfo : layer.multidimensionalInfo;
    const multidimensionalSubset = layer.multidimensionalSubset;
    if (mutlidimensionalInfo !== undefined && mutlidimensionalInfo !== null) {
      const variables =
        multidimensionalSubset !== null && multidimensionalSubset !== undefined
          ? // FIXME: Method is still missing in typings
            (layer as any).getMultidimensionalSubsetVariables()
          : mutlidimensionalInfo.variables;
      if (variables !== undefined && variables.length > 0) {
        const dimensions: string[] = [];
        const variableFields: FieldInfo[] = [];
        const dimensionVariables: DimensionVariables = {};
        const dimensionValues: DimensionValues = {};
        const dimensionVariableDatasets: { [key: string]: Dataset } = {};
        const dimensionMinValues: ProcessedLayerProperties = {};
        const dimensionMaxValues: ProcessedLayerProperties = {};
        const dimensionMinValuesWithLabels: ProcessedLayerProperties = {};
        const dimensionMaxValuesWithLabels: ProcessedLayerProperties = {};
        variables.forEach((variable: __esri.RasterMultidimensionalInfoVariables, vidx: number) => {
          let dimensionsDescription = "";
          const variableName = variable.name;
          variable.dimensions.forEach((dimension, index) => {
            const dimensionName = dimension.name;
            if (dimensions.indexOf(dimensionName) === -1) {
              dimensions.push(dimensionName);
            }
            const dimensionDescription =
              index > 0
                ? `,${dimensionName}=${dimension.values?.length}`
                : `${dimensionName}=${dimension.values?.length}`;
            dimensionsDescription = dimensionsDescription.concat(dimensionDescription);
            if (index === 0) {
              dimensionValues[variableName] = {};
            }
            if (dimensionVariables[dimensionName] === undefined) {
              dimensionVariables[dimensionName] = [];
            }
            dimensionValues[variableName][dimensionName] = dimension.values;
            // only set values from first variable
            if (vidx === 0) {
              const values = dimension.values;
              if (values !== undefined) {
                if (dimensionName === "StdTime" && (dimension.unit === undefined || dimension.unit === "ISO8601")) {
                  const dimMinValues: string[] = [];
                  const dimMaxValues: string[] = [];
                  const dimMinValuesWithLabels: ProcessedLayerProperties = {};
                  const dimMaxValuesWithLabels: ProcessedLayerProperties = {};
                  values.forEach((value: number[] | number) => {
                    if (typeof value === "number") {
                      const date = new Date(value);
                      const label = date.toLocaleString();
                      const val = date.toISOString();
                      dimMinValues.push(val);
                      dimMaxValues.push(val);
                      dimMinValuesWithLabels[val] = label;
                      dimMaxValuesWithLabels[val] = label;
                    } else if (Array.isArray(value)) {
                      const d1 = new Date(value[0]);
                      const d2 = new Date(value[1]);
                      const val1 = d1.toISOString();
                      const val2 = d2.toISOString();
                      dimMinValues.push(val1);
                      dimMaxValues.push(val2);
                      dimMinValuesWithLabels[val1] = d1.toLocaleString();
                      dimMaxValuesWithLabels[val2] = d2.toLocaleString();
                    }
                  });
                  dimensionMinValues[dimensionName] = dimMinValues;
                  dimensionMaxValues[dimensionName] = dimMaxValues;
                  dimensionMinValuesWithLabels[dimensionName] = dimMinValuesWithLabels;
                  dimensionMaxValuesWithLabels[dimensionName] = dimMaxValuesWithLabels;
                } else {
                  if (Array.isArray(values) && typeof values[0] === "number") {
                    dimensionMinValues[dimensionName] = dimensionMaxValues[dimensionName] = values.map(String);
                  } else if (Array.isArray(values[0]) && Array.isArray(values[1])) {
                    dimensionMinValues[dimensionName] = values[0].map(String);
                    dimensionMaxValues[dimensionName] = values[1].map(String);
                  }
                }
              }
            }
            dimensionVariables[dimensionName].push(variableName);
          });
          variableFields.push({
            name: variableName,
            alias: `${variableName}[${dimensionsDescription}](${variable.description})`,
            type: "string"
          });
        });
        for (const key in dimensionVariables) {
          const filteredFields = variableFields.filter(
            (variableKey) => dimensionVariables[key].indexOf(variableKey.name) > -1
          );
          dimensionVariableDatasets[key] = {
            id: "1",
            name: key,
            description: "DimensionVariables",
            getFields: () => Promise.resolve(filteredFields)
          };
        }
        const variablesDataset: Dataset = {
          id: "1",
          name: "Variables",
          description: "Variables",
          getFields: () => Promise.resolve(variableFields)
        };
        processedProperties = {
          isMultidimensional: true,
          dimensions: dimensions,
          dimensionVariableDatasets: dimensionVariableDatasets,
          dimensionValues: dimensionValues,
          variablesDataset: variablesDataset,
          dimensionMinValues: dimensionMinValues,
          dimensionMaxValues: dimensionMaxValues,
          dimensionMinValuesWithLabels: dimensionMinValuesWithLabels,
          dimensionMaxValuesWithLabels: dimensionMaxValuesWithLabels
        };
      }
    }

    const rasterInfo =
      layer.type === "imagery" ? await layer.generateRasterInfo(layer.renderingRule) : layer.rasterInfo;
    processedProperties.rasterInfo = rasterInfo;

    return processedProperties;
  }
);

/**
 * @category Public
 * @param {__esri.Portal} portal
 * @param signal
 * @param signal.signal
 * @param {__esri.PortalItem} item - the deep learning model item to be processed
 * @returns {ProcessedDataProperties }
 * process deep learning item to get key-value infos
 */
export async function processDeepLearningModelItem(
  item: any,
  portal: __esri.Portal,
  signal?: { signal: AbortSignal }
): Promise<ProcessedDataProperties> {
  const dataTypeMap: ProcessedDataProperties = {
    string: "GPString",
    numeric: "GPDouble"
  };
  let parameterInfos: ModelParameters[] = [];
  const value: ProcessedDataProperties = {};
  let modelType;
  let modelInfo: any;
  if (item.sourceJSON?.properties !== undefined && item.sourceJSON?.properties !== null) {
    modelInfo = cloneDeep(item.sourceJSON.properties);
  } else {
    try {
      const gpjob = await geoprocessor.submitJob(
        getDeepLearningQueryUrl(portal),
        {
          model: { itemId: item.id }
        },
        undefined,
        signal
      );
      const jobInfo = await gpjob.waitForJobCompletion(signal);
      const result = await jobInfo.fetchResultData("outModelInfo", undefined, signal);
      modelInfo = result?.value;
      item.update();
    } catch (err) {
      console.error("fetching model item failed");
      if (signal?.signal.aborted === true) {
        throw new ArcgisWebAnalysisError("AbortError");
      }
      throw err;
    }
  }
  if (modelInfo !== undefined && modelInfo !== null) {
    const modelParamInfo = modelInfo.modelInfo?.ParameterInfo;
    modelType = modelInfo.modelInfo?.ModelType;
    const paramsToAvoid = ["raster", "rasters", "model", "device"];
    const params = modelParamInfo?.filter((param: ModelParameters) => {
      return (
        Object.keys(dataTypeMap).includes(param.dataType) &&
        param.required === "0" &&
        paramsToAvoid.indexOf(param.name.toLowerCase()) < 0
      );
    });
    parameterInfos = params?.map((param: ModelParameters) => {
      param.dataType = dataTypeMap[param.dataType];
      param.required = false; // we filter out params which are not required, so the value will be always false
      return param;
    });
    if (parameterInfos !== undefined && parameterInfos?.length > 0) {
      parameterInfos.forEach((param: ModelParameters) => {
        const pname = param.name;
        value[pname] = param.value;
      });
    }
  }
  return { parameterInfos: parameterInfos, value: value, modelType };
}

/**
 * @category Public
 * @param params `AnalyzeGeocodeInput` parameters + the url of the `asyncGeocode` helperService
 */
export const analyzeGeocodeInput = async (params: GetToolUIAddressFieldsParams): Promise<AnalyzeGeocodeResult> => {
  const { asyncGeocodeUrl } = params;
  const jobInfo = await geoprocessor.submitJob(`${asyncGeocodeUrl}/AnalyzeGeocodeInput`, {
    ...omit(params, "asyncGeocodeUrl"),
    f: "json"
  });
  try {
    await jobInfo.waitForJobCompletion();
    const result = await jobInfo.fetchResultData("geocodeParameters");
    return result.value as unknown as AnalyzeGeocodeResult;
  } catch (err) {
    console.error("AnalyzeGeocodeInput failed");
    throw err;
  }
};
