/**
 * Explains the Analysis tool execution type.
 */
export const AnalysisToolExecutionType = {
  /**
   * Tool execution will be asynchronous.
   */
  Asynchronous: "esriExecutionTypeAsynchronous",
  /**
   * Tool execution will be synchronous.
   */
  Synchronous: "esriExecutionTypeSynchronous"
} as const;
export type AnalysisToolExecutionType = typeof AnalysisToolExecutionType[keyof typeof AnalysisToolExecutionType];
