import { memoize, omit } from "lodash-es";

import {
  AnalysisEngine,
  AnalysisToolData,
  AnalysisToolInfo,
  AnalysisToolParam,
  AnalysisToolParamDirection,
  AnalysisToolUI,
  AnalysisToolUICurrentVersion,
  AnalysisToolUIParam,
  LocaleItem
} from "analysis-ui-schema";

import { isEmptyDataItem } from "../common";
import { SupportedLocales } from "../localization/interfaces";
import { GetUIOnlyParamsProps, GetUIOnlyParamsToDisplayProps } from "../interfaces";

import { AnalysisToolUIAssets, DefaultUIOnlyParamKeys, DefaultUIOnlyParamKeysForDisplay } from "./interfaces";

/**
 * Gets all of the parameters (key-value pairs) from the given ToolUI.
 * @param {AnalysisToolUI["UIparameters"] uiParameters} uiParameters
 * @returns {[keyof: string]: string} parameter label object
 */
export function getUIParamsByName(uiParameters: AnalysisToolUI["UIparameters"]): {
  [key: string]: AnalysisToolUIParam;
} {
  const paramsByName: { [key: string]: AnalysisToolUIParam } = {};
  for (const uiParameter of uiParameters) {
    if (uiParameter.UIparameters !== undefined) {
      // nested case, recursive
      Object.assign(paramsByName, getUIParamsByName(uiParameter.UIparameters));
    } else if (uiParameter.name !== undefined) {
      // default case
      if (uiParameter.name !== undefined) {
        paramsByName[uiParameter.name] = uiParameter;
      }
    }
  }
  return paramsByName;
}
/**
 * Returns the UI only parameters (includes ui params we don't want to show in history parameter list)
 * @param {AnalysisToolData} toolData the current tool data containing all the parameters
 * @param {string[]} keysToRemove the expected job parameters for the tool, or other keys to remove from the toolData
 * @param {string[]} partialKeysToRemove keys to be removed from the toolData if they are partial matches (ex. "_SelectedLayers")
 * @param {boolean} isWebTool whether or not the related tool is a webtool, defaults to false
 * @param props
 * @returns {AnalysisToolData} UI only parameters including
 */
export function getUIOnlyParams(props: GetUIOnlyParamsProps): AnalysisToolData {
  const { toolData, keysToRemove } = props;
  const isWebTool = props.isWebTool ?? false;
  const partialKeysToRemove = props.partialKeysToRemove ?? [];
  const { ExcludedUIOnlyParamKeys, ExcludedUIOnlyParamPartialKeys, AlwaysIncludedUIOnlyParamKeys } =
    DefaultUIOnlyParamKeys;

  // Save a copy of all always included key values, these will be added back after the filter.
  const alwaysIncludedUIOnlyParams: AnalysisToolData = {};
  if (!isWebTool) {
    for (const key of AlwaysIncludedUIOnlyParamKeys) {
      // Note: Even if the value is undefined, we still want to save it.
      alwaysIncludedUIOnlyParams[key] = toolData[key];
    }
  }

  // Remove all keys that are expected to be in the job parameters. As well as
  // any keys that are explicitly excluded.
  const allExcludedKeys = [...ExcludedUIOnlyParamKeys, ...keysToRemove];
  const allExcludedPartialKeys = [...ExcludedUIOnlyParamPartialKeys, ...partialKeysToRemove];
  // Find all substring matches for the partial keys
  const keySubstringMatches = Object.keys(toolData).filter((key) =>
    allExcludedPartialKeys.some((partialKey) => key.includes(partialKey))
  );

  const allParamKeysToRemove = [...allExcludedKeys, ...keySubstringMatches];

  const toolUIOnlyParams: AnalysisToolData = omit(toolData, allParamKeysToRemove);

  const removeEmptyItem = (key: string) => {
    if (isEmptyDataItem(toolUIOnlyParams[key])) delete toolUIOnlyParams[key];
  };
  Object.keys(toolUIOnlyParams).forEach(removeEmptyItem);

  // Add back in the always included keys
  return { ...toolUIOnlyParams, ...alwaysIncludedUIOnlyParams };
}

/**
 * Returns the UI only parameters that we *want* to show in the history parameter list
 * This is a subset of the UI only parameters, as well as "alwaysShown" parameters that
 * we add back in for the history parameter list (even when they are not in the UI only params)
 * @param {AnalysisToolData} combinedParams the tool data containing all the parameters
 * @param {string} toolName the name of the tool { "Aggregate Points" | "Create Buffers" | "Enrich Layer" | etc }
 * @param {boolean} isWebTool whether or not the related tool is a webtool, defaults to false
 * @param props
 * @returns {AnalysisToolData} the ui only params that are expected to be shown in the history parameter list
 */
export function getUIOnlyParamsToDisplay(props: GetUIOnlyParamsToDisplayProps): AnalysisToolData {
  const { combinedParams, toolName } = props;
  const isWebTool = props.isWebTool ?? false;
  const { ExcludedUIOnlyParamKeysForUI, ExcludedUIOnlyParamPartialKeysForUI } = DefaultUIOnlyParamKeysForDisplay;

  let uiOnlyParamsToDisplay = getUIOnlyParams({
    toolData: combinedParams,
    keysToRemove: ExcludedUIOnlyParamKeysForUI,
    partialKeysToRemove: ExcludedUIOnlyParamPartialKeysForUI,
    isWebTool
  });
  // TODO: we need a way to generalize replace as needed below for enrich layer like we do with omit using `getUIOnlyParams`.
  // one idea we have analysis-tool-history-display.json
  // where we can have these rules for each tool as needed and also the general exclude rules
  // {
  //     EnrichLayer  analysisVariables labels replace
  //      *           saveResultIn include
  //      *           _selectLayersKey exclude
  // } similar to mergeAttributes in merge layer.
  if (uiOnlyParamsToDisplay.analysisVariables !== undefined && uiOnlyParamsToDisplay.labels !== undefined) {
    uiOnlyParamsToDisplay.analysisVariables = uiOnlyParamsToDisplay.labels;
    uiOnlyParamsToDisplay = omit(uiOnlyParamsToDisplay, "labels");
  }

  if (uiOnlyParamsToDisplay.hierarchy !== undefined && uiOnlyParamsToDisplay.hierarchyAlias !== undefined) {
    uiOnlyParamsToDisplay.hierarchy = uiOnlyParamsToDisplay.hierarchyAlias;
  }
  uiOnlyParamsToDisplay = omit(uiOnlyParamsToDisplay, "hierarchyAlias");

  if (uiOnlyParamsToDisplay.country !== undefined && uiOnlyParamsToDisplay.countryAlias !== undefined) {
    uiOnlyParamsToDisplay.country = uiOnlyParamsToDisplay.countryAlias;
  }
  uiOnlyParamsToDisplay = omit(uiOnlyParamsToDisplay, "countryAlias");

  // similar issue as seen with EnrichLayer mentioned above
  // we have to exclude a tool level UI parameter for ExtractData resultType
  // {
  //    ExtractData resultType exclude
  //
  // }
  // As mentioned in the above TODO: we need this tool level operation to be done here. As tool name is not passed using
  // this combination to exclude it
  if (uiOnlyParamsToDisplay.resultType !== undefined && uiOnlyParamsToDisplay.dataFormat !== undefined) {
    uiOnlyParamsToDisplay = omit(uiOnlyParamsToDisplay, "resultType");
  }
  // Similar to the comments above though this is slightly more complex, however it could be handled in the same way
  // this section handles the Choose Best Facilities tool
  if (toolName === "ChooseBestFacilities") {
    const uiParamPartialsToRemove = ["_maxTravelRangeChoice", "_requiredFacilitiesCapacityChoice", "_demandChoice"];
    const paramPartialMatches = Object.keys(uiOnlyParamsToDisplay).filter((key) =>
      uiParamPartialsToRemove.some((partialKey) => key.includes(partialKey))
    );
    uiOnlyParamsToDisplay = omit(uiOnlyParamsToDisplay, ["previousWorkflow", ...paramPartialMatches]);
  }

  return uiOnlyParamsToDisplay;
}

/**
 * Returns the tool name as pascal case from camel case
 * e.g. "createBuffers" -> "CreateBuffers"
 * @param {string} tooName
 * @param toolName
 * @returns {string}
 */
export const toPascalToolName = (toolName: string) => `${toolName.charAt(0).toUpperCase()}${toolName.slice(1)}`;

/**
 * Returns the tool name as camel case from pascal case
 * e.g. "CreateBuffers" -> "createBuffers"
 * @param {string} tooName
 * @param toolName
 * @returns {string}
 */
export const toCamelToolName = (toolName: string) => `${toolName.charAt(0).toLowerCase()}${toolName.slice(1)}`;

/**
 * This returns the suffix to be used for toolUIJson and toolT9n files
 * @param {AnalysisEngine} analysisEngine the analysis engine
 * @returns {string} the suffix
 */
export function formatAnalysisEngineSuffix(analysisEngine?: AnalysisEngine): string {
  analysisEngine ??= AnalysisEngine.Standard;

  let analysisEngineSuffix = "";
  switch (analysisEngine) {
    case AnalysisEngine.Raster:
      analysisEngineSuffix = "_ra";
      break;
    default:
    case AnalysisEngine.GeoAnalytics:
    case AnalysisEngine.Standard:
      analysisEngineSuffix = "";
      break;
  }
  return analysisEngineSuffix;
}

/**
 * Gets both the toolUIJson and the tool T9n for a given tool name and locale
 * @param {SupportedLocales} locale the current locale
 * @param {string} name the tool's name
 * @param {(path: string) => Promise<any>} memoizeHelper, a helper function to memoize the fetch requests
 * @returns {Promise<{ toolUIJson: AnalysisToolUI, toolT9n: AnalysisToolT9n }>} the tool json and t9n
 */
export const getToolJSONs = memoize(
  async (path: {
    locale: SupportedLocales;
    name: string;
    analysisEngine?: AnalysisEngine;
    assetPrefix: string;
    memoizedHelper: (path: string) => Promise<any>;
  }): Promise<AnalysisToolUIAssets> => {
    const { locale, name, assetPrefix, analysisEngine, memoizedHelper } = path;

    const lowercaseToolName = `${name.toLowerCase()}${formatAnalysisEngineSuffix(analysisEngine)}`;

    let toolUIJson: AnalysisToolUI | undefined;
    let toolT9n: LocaleItem | undefined;
    let commonT9n: LocaleItem | undefined;
    let toolJson: AnalysisToolInfo | undefined;

    const toolPath = `${assetPrefix}/tool-ui-json/${lowercaseToolName}.tool.ui.json`;
    const toolJSONPath = `${assetPrefix}/tool-json/${lowercaseToolName}.tool.json`;
    const t9nPath = `${assetPrefix}/t9n/${lowercaseToolName}/${lowercaseToolName}.t9n.${locale}.json`;
    const commonT9nPath = `${assetPrefix}/t9n/common.t9n.${locale}.json`;
    let helpLocale = locale as string;
    if (locale === SupportedLocales.English || locale === SupportedLocales.EnglishUSA) {
      helpLocale = "";
    } else if (helpLocale === SupportedLocales.NorwegianBokml) {
      // FIX: we should let t9n team to change help HTML files to use nb for norwegian-bokamal(nb) locale we support,
      // so this mapping is not needed.
      helpLocale = "no";
    }
    const toolHelpFilePath = `${assetPrefix}/help/${
      helpLocale !== "" ? helpLocale + "/" : ""
    }${name}${formatAnalysisEngineSuffix(analysisEngine)}.html`;
    try {
      toolJson = (await memoizedHelper(toolJSONPath)) as AnalysisToolInfo;
      toolUIJson = (await memoizedHelper(toolPath)) as AnalysisToolUI;
      toolT9n = (await memoizedHelper(t9nPath)) as LocaleItem;
      commonT9n = (await memoizedHelper(commonT9nPath)) as LocaleItem;
      toolT9n = { ...commonT9n, ...toolT9n };
    } catch {
      toolUIJson = undefined;
    }
    return { toolJson: toolJson as AnalysisToolInfo, toolUIJson, toolT9n, toolHelpFilePath };
  }
);

/**
 *
 * @param webToolJSON
 * @param commonT9nPath
 * @param memoizedHelper
 */
export async function getWebToolUIJson(
  webToolJSON: AnalysisToolInfo,
  commonT9nPath: string,
  memoizedHelper: (path: string) => Promise<any>
): Promise<AnalysisToolUI> {
  let commonT9n: LocaleItem | undefined;
  try {
    commonT9n = (await memoizedHelper(commonT9nPath)) as LocaleItem;
  } catch {
    throw new Error(`Failed to fetch asset from path: "${commonT9nPath}"`);
  }
  const datasetInputParamsMap: Record<string, AnalysisToolParam> = {};
  const webToolsUnits = commonT9n.webToolsUnits;
  const inputParams: AnalysisToolParam[] = [];
  const inputUIParameters: AnalysisToolUIParam[] = [];
  let analysisBlock;
  for (const param of webToolJSON.parameters ?? []) {
    // custom web tools don't accept output parameters
    if (param.direction === AnalysisToolParamDirection.Input) {
      const inputUIParam: AnalysisToolUIParam = {
        name: param.name,
        label: param.displayName ?? param.name
      };
      if (param.name === "esri_out_feature_service_name" && param.dataType === "GPString") {
        analysisBlock = {
          componentName: "analysis-block",
          label: commonT9n.resultLayerGroupLabel as unknown as string,
          summaryText: commonT9n.resultLayerGroupText,
          UIparameters: [inputUIParam]
        };
      } else {
        if (param.dataType === "GPFeatureRecordSetLayer" || param.dataType === "GPRecordSet") {
          datasetInputParamsMap[param.name] = param;
        } else {
          inputParams.push(param);
          if (param.dataType === "GPLinearUnit") {
            // add default units based on https://developers.arcgis.com/enterprise-sdk/api-reference/java/com/esri/arcgis/system/esriUnits.html
            inputUIParam.choiceList = Object.keys(webToolsUnits);
            inputUIParam.choiceListLabels = webToolsUnits as { [key: string]: string };
          } else if (param.dataType === "GPMultiValue:GPLinearUnit") {
            inputUIParam.UIparameterInfos = [
              {
                name: "",
                choiceList: Object.keys(webToolsUnits),
                choiceListLabels: webToolsUnits as { [key: string]: string }
              }
            ];
          }
        }

        inputUIParameters.push(inputUIParam);
      }
    }
  }

  // provide a default tool ui json with all parameters inside an analysis block component
  const toolUIJson: AnalysisToolUI = {
    toolName: webToolJSON.name,
    version: AnalysisToolUICurrentVersion,
    UIparameters: [] as AnalysisToolUIParam[]
  };

  if (!isEmptyDataItem(inputUIParameters)) {
    toolUIJson.UIparameters.push({
      // label parameter is not added for a single default block
      // we can potentially add more blocks by reading category information of the toolJSON
      componentName: "analysis-block",
      UIparameters: inputUIParameters
    });
  }

  // Special case analysis-block, push to end of parameters
  if (analysisBlock !== undefined) {
    toolUIJson.UIparameters.push(analysisBlock);
  }

  // create rules based on dependency between Field and GPFeatureRecordSetLayer / GPRecordSet input params
  for (const param of inputParams) {
    let dependsOn: AnalysisToolParam | undefined;
    if (param.dependency !== undefined) {
      dependsOn = datasetInputParamsMap[param.dependency];
    }
    if ((param.dataType === "Field" || param.dataType === "GPMultiValue:Field") && dependsOn !== undefined) {
      if (toolUIJson.rules === undefined) {
        toolUIJson.rules = [];
      }
      toolUIJson.rules.push({
        ruleType: "expression",
        expression: `$[Data].${dependsOn.name} NOT null`,
        destination: param.name,
        effectParams: {
          mapLayer: `$[Component].${dependsOn.name}.selectedLayers`
        }
      });
    }
  }
  return toolUIJson;
}

/**
 * Determines if travel mode is used in this tool.
 * @category Public
 * @param {string} toolName
 * @returns {boolean}
 */
export function isTravelModeUsedByTool(toolName: string): boolean {
  const supportedToolNames = [
    "ChooseBestFacilities",
    "ConnectOriginsToDestinations",
    "CreateDriveTimeAreas",
    "EnrichLayer",
    "FindNearest",
    "PlanRoutes",
    "SummarizeNearby"
  ];
  return supportedToolNames.includes(toolName);
}

export const StraightLineParameterValue = "StraightLine";

/**
 * Checks if the parameter is a travel mode parameter or not.
 * @param paramName
 * @param toolName
 * @returns boolean Return value is true or false for the check if its a travel mode based parameter or not.
 */
export function isTravelModeParameter(paramName: AnalysisToolParam["name"], toolName: string): boolean {
  const knownTravelModeParameters = ["travelMode", "nearType", "measurementType", "bufferType"];
  // Initially checking if the tool name and parameter is in the list of known tool names and parameter names.
  let isTravelModeParam = isTravelModeUsedByTool(toolName) && knownTravelModeParameters.includes(paramName);
  // This Switch case further checks if the toolName and its travel mode parameter match.
  switch (toolName) {
    case "SummarizeNearby":
      isTravelModeParam = isTravelModeParam && paramName === "nearType";
      break;
    case "EnrichLayer":
      isTravelModeParam = isTravelModeParam && paramName === "bufferType";
      break;
    case "ConnectOriginsToDestinations":
    case "FindNearest":
    case "FindClosest":
      isTravelModeParam = isTravelModeParam && paramName === "measurementType";
      break;
    case "PlanRoutes":
    case "CreateDriveTimeAreas":
    case "ChooseBestFacilities":
      isTravelModeParam = isTravelModeParam && paramName === "travelMode";
      break;
    default:
      // we want to default to false if none of the above conditions match.
      isTravelModeParam = false;
      break;
  }
  return isTravelModeParam;
}
