/**
 * ArcgisWebAnalysisError is a class that enhances the debugging and error handling process. Rather than returning a generic [JavaScript
 * error](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Error),
 * this ArcgisWebAnalysisError returns a standardized error object with several properties.
 */
export class ArcgisWebAnalysisError<T = any> extends Error {
  /**
   * A unique error name. This can be used to map to a localized error message to present to the user.
   *
   */
  name: string;

  /**
   * The details object provides additional details specific to the error,
   * giving more information about why the error was raised. For example, the details object includes
   * additional information to help the developer diagnose issues with a problematic request.
   *
   */
  details?: T;

  /**
   * Cause of the error. This is useful when the error is a result of another error.
   * For example, if a request to a server fails, the cause of the error is the error returned by the server.
   */
  cause?: Error;

  /**
   * Creates a new ArcgisWebAnalysisError instance.
   * @param {string} name - A unique error name.
   * @param {string} [message] - A message describing the details of the error.
   * @param {object} [details] - The details object provides additional details specific to the error.
   * @param {Error} [cause] - Cause of the error.
   */
  constructor(name: string, message?: string, details?: T, cause?: Error) {
    super(message);

    this.name = name;
    this.details = details;
    this.cause = cause; // TODO: should be able to make use of builtin cause property
  }
}

/**
 * Check if the error was from an aborted operation.
 * @param error
 */
export function wasAborted(error: unknown): boolean {
  return error instanceof Error && error.name === "AbortError";
}

export const DEFAULT_ERROR_KEY = "default";
export const DEFAULT_CREDIT_ERROR_KEY = "creditsDefault";
