/*
  1. Authenticate user
  2. create service
  3. submit job
  4. updates item
  5. get Result
  publish events during the analysis workflows
*/

import JobInfo from "@arcgis/core/rest/support/JobInfo";
import * as geoprocessor from "@arcgis/core/rest/geoprocessor";
import esriRequest from "@arcgis/core/request";

import {
  AnalysisJobStatus,
  AnalysisSupportedItemTypes,
  CompletedStatuses,
  ErrorKeywords,
  buildProcessInfoReport,
  checkUserAccess,
  isEmptyDataItem,
  isStatusCancellable,
  replaceSpacesWithUnderscores,
  throwError,
  updateItemProperties
} from "analysis-shared-utils";
import { AnalysisEngine, AnalysisToolExecutionType, ItemId } from "analysis-ui-schema";

import { getAnalysisServer } from "../tools";
import { notifyJobStatus, notifyResultData } from "../events";
import { creditThresholdCheck, getCreditsEstimate, hasEnoughCredits, parseCreditEstimate } from "../credits";

import { CheckServiceNameProps, ExecuteProps, validOutputNameCharactersPattern } from "./interfaces";

import type ParameterValue from "@arcgis/core/rest/support/ParameterValue";

/**
 * Get service name from output name parameters and job parameters
 * @param {string[]} outputNameParams - The job parameter keys that contain the output names
 * @param {ExecuteProps["jobParams"]} jobParams - The job parameters
 * @returns {string[]} List of service names
 */
export function getOutputServiceNames(outputNameParams: string[], jobParams: ExecuteProps["jobParams"]): string[] {
  const serviceNames: string[] = [];
  outputNameParams.forEach((outputNameParam) => {
    if (outputNameParam in jobParams) {
      const outputName = JSON.parse(jobParams[outputNameParam] as string);
      const serviceName = outputName?.serviceProperties?.name ?? outputName?.itemProperties?.title ?? undefined;
      if (serviceName !== undefined) {
        serviceNames.push(serviceName);
      }
    }
  });

  // Special case handling for esri_out_feature_service_name
  // https://devtopia.esri.com/WebGIS/arcgis-web-analysis/issues/4509
  if ("esri_out_feature_service_name" in jobParams && typeof jobParams.esri_out_feature_service_name === "string") {
    serviceNames.push(jobParams.esri_out_feature_service_name);
  }

  return serviceNames;
}

/**
 * checks if the service name is available for the type of service ( Feature Service or Image Service) in an organization.
 * @param {CheckServiceNameProps} props the parameters for checkServiceName, see type for more info.
 * @returns {Promise<boolean>} returns true if the service name is available in the organization, false otherwise.
 */
export async function checkServiceName(props: CheckServiceNameProps): Promise<boolean> {
  const {
    portal: { url: portalUrl, id },
    itemType
  } = props;
  const { serviceName } = props;
  const url = `${portalUrl}/sharing/rest/portals/${id}/isServiceNameAvailable?f=json`;
  const options = {
    query: {
      f: "json",
      name: serviceName,
      type: itemType
    }
  };
  let isAvailable = true;
  try {
    const response = await esriRequest(url, options);
    isAvailable = response.data.available;
  } catch (e) {
    await throwError(ErrorKeywords.NetworkError);
  }
  return isAvailable;
}

/**
 * Checks if the output name contains any invalid characters
 * @param {string[]} outputServiceNames The output service names
 * @returns {boolean} Whether or not the output names are valid
 * @see validOutputNameCharactersPattern
 *
 * exported for testing
 */
export function outputNameValidation(outputServiceNames: string[]): boolean {
  const isOutputNameValid = outputServiceNames.every((outputServiceName) =>
    validOutputNameCharactersPattern.test(outputServiceName)
  );

  return isOutputNameValid;
}

async function addProcessInfoToItems(results: ParameterValue[], processInfo?: string): Promise<void> {
  const itemUpdatePromises: Promise<__esri.PortalItem>[] = [];
  results.forEach((result) => {
    // The result must have an item id for the process info to be added to the description
    const hasItemId =
      typeof result.value === "object" && "itemId" in result.value && !isEmptyDataItem(result.value.itemId);
    if (hasItemId && processInfo !== undefined) {
      const itemId = (result.value as ItemId).itemId;
      itemUpdatePromises.push(updateItemProperties(itemId, { description: processInfo }));
    }
  });
  await Promise.all(itemUpdatePromises);
}

/**
 * Execute method is called by the analysis tools to start the analysis
 * @param {ExecuteProps} params
 *
 * TODO: Look into refactoring to a job manager class that's de-coupled from DOM
 */
export async function execute(params: ExecuteProps): Promise<void> {
  const { portal } = params;
  const {
    jobParamsPayload,
    jobParams,
    toolUiParameters,
    toolName,
    analysisEngine,
    analysisType,
    resultParams,
    containerElement,
    gpMessages,
    webToolServerUrl,
    executionType,
    creditCheckOptions
  } = params;
  let canDoAnalysis = checkUserAccess(portal.user);
  // outputName needs to be defined for creating service item.
  // serviceProperties to be defined when creating FS or IS items.
  const hasServiceProperties = Object.keys(jobParamsPayload).some((paramName) => {
    const paramValue = jobParamsPayload[paramName];
    return typeof paramValue === "string" && paramValue.includes("serviceProperties");
  });
  const outputNameParams = Object.keys(jobParamsPayload).filter((paramName) => {
    const paramValue = jobParamsPayload[paramName];
    return (
      typeof paramValue === "string" &&
      (paramValue.includes("serviceProperties") || paramValue.includes("itemProperties"))
    );
  });
  const outputServiceNames = getOutputServiceNames(outputNameParams, jobParamsPayload);
  // do client side validation first
  const isOutputServiceNameValid = outputNameValidation(outputServiceNames);

  if (isOutputServiceNameValid === false) {
    await throwError(ErrorKeywords.SpecialCharactersInServiceNameError);
  }

  // check server if the name exists using isServiceNameAvailableCheck
  // the server also does validation on the name.
  // only when outputName has serviceProperties defined we check isServiceNameAvailableCheck
  if (hasServiceProperties) {
    const checkServiceNamePromises = outputServiceNames.map((serviceName) => {
      const formattedServiceName = portal.isPortal ? replaceSpacesWithUnderscores(serviceName) : serviceName;
      return checkServiceName({
        portal,
        serviceName: formattedServiceName,
        itemType:
          analysisEngine === AnalysisEngine.Raster &&
          (toolUiParameters.resultType === undefined || toolUiParameters.resultType !== "FEATURE_LAYER")
            ? AnalysisSupportedItemTypes.ImageService
            : AnalysisSupportedItemTypes.FeatureService
      });
    });
    const resolvedServiceNameChecks = await Promise.all(checkServiceNamePromises);
    canDoAnalysis = !resolvedServiceNameChecks.some((checks) => checks === false);
    if (canDoAnalysis === false) {
      await throwError(ErrorKeywords.ServiceNameExistsError);
    }
  }
  // check available credits for the user for credit-based tools.
  // credits are not checked for web tools ( enterprise or online)
  const { skipCreditChecks = false, creditThreshold } = creditCheckOptions ?? {};
  if (
    skipCreditChecks === false &&
    webToolServerUrl === undefined &&
    (params.analysisEngine === AnalysisEngine.Standard || params.analysisEngine === AnalysisEngine.Raster)
  ) {
    const creditResults = await getCreditsEstimate({
      toolName: params.toolName,
      portal,
      jobParams: params.jobParamsPayload,
      analysisEngine: params.analysisEngine,
      toolElement: params.containerElement
    });
    const estimatedCredits = parseCreditEstimate(creditResults);

    // First check that the user has enough credits to run the analysis
    canDoAnalysis = hasEnoughCredits(estimatedCredits, portal.user);
    if (canDoAnalysis === false) {
      await throwError(ErrorKeywords.InsufficientCreditsError);
    }

    // Then check that the estimated credits is below the threshold
    // If the threshold is undefined, then it is ignored
    canDoAnalysis = creditThresholdCheck(estimatedCredits, creditThreshold);
    if (canDoAnalysis === false) {
      await throwError(ErrorKeywords.CreditThresholdExceededError, {
        estimatedCredits: estimatedCredits,
        creditThreshold: creditThreshold
      });
    }
  }

  if (canDoAnalysis === true) {
    const analysisServer =
      webToolServerUrl === undefined
        ? getAnalysisServer(analysisEngine, portal, toolName === "BatchGeocode")
        : webToolServerUrl;
    const toolUrl = `${analysisServer}/${toolName}?f=json`;
    if (executionType === AnalysisToolExecutionType.Synchronous) {
      const response = await geoprocessor.execute(toolUrl, jobParams);
      const results: ParameterValue[] = response.results;
      const jobInfoSync = JobInfo.fromJSON({ messages: response.messages });
      notifyJobStatus(containerElement, {
        jobInfo: jobInfoSync,
        submissionData: {
          toolName: toolName,
          analysisEngine: analysisEngine,
          analysisType: analysisType,
          jobParams: jobParams,
          toolUiParameters: toolUiParameters
        }
      });
      notifyResultData(containerElement, {
        results: results,
        jobInfo: jobInfoSync
      });
    } else {
      const jobInfo = await geoprocessor.submitJob(toolUrl, jobParamsPayload);
      notifyJobStatus(containerElement, {
        jobInfo: jobInfo,
        submissionData: {
          toolName: toolName,
          analysisEngine: analysisEngine,
          analysisType: analysisType,
          jobParams: jobParams,
          toolUiParameters: toolUiParameters,
          webToolServerUrl
        }
      });

      const options = {
        interval: 1500, // TODO: Create external config setting for interval (issue #725)
        statusCallback: (j: JobInfo) => {
          notifyJobStatus(containerElement, { jobInfo: j });
        }
      };
      try {
        await jobInfo.waitForJobCompletion(options);
        let processInfo: string | undefined;
        if (resultParams.includes("processInfo") === true) {
          try {
            const processInfoParamValue = await jobInfo.fetchResultData("processInfo");
            processInfo = buildProcessInfoReport(<string[]>processInfoParamValue.value, gpMessages);
            // We have processed it specially, delete in place
            const indexToDelete = resultParams.indexOf("processInfo");
            resultParams.splice(indexToDelete, 1);
          } catch {
            // no-op, process the rest of the results on fail
          }
        }
        const resultPromises = resultParams.map((resultParam) => jobInfo.fetchResultData(resultParam));
        const allResults = await Promise.allSettled(resultPromises);
        const results = allResults
          .filter((res) => res.status === "fulfilled")
          .map((fulfilledRes: PromiseFulfilledResult<ParameterValue>) => fulfilledRes.value);
        // Do not await so the result notifying is not waiting for the portal item to load
        addProcessInfoToItems(results, processInfo);
        notifyResultData(containerElement, { results: results, jobInfo: jobInfo });
      } catch (e) {
        notifyJobStatus(containerElement, { jobInfo: jobInfo });
      }
    }
  }
}

/**
 * This cancels a job and returns the updated jobInfo for that job.
 * Developers note: JSAPI's designs revolves around sending back the jobInfo of a cancelled job
 * in an error. This is why we call `cancelJob` and then `waitForJobCompletion` in a try catch.
 * We can expect to see properties of jobInfo in this error.
 * @param {JobInfo} jobInfo the jobInfo for the job that should be cancelled.
 * @param containerElement
 * @returns {JobInfo} the updated jobInfo for the job that was cancelled.
 */
export async function cancelJob(
  jobInfo: JobInfo,
  containerElement: HTMLElement
): Promise<{ jobStatus: JobInfo["jobStatus"]; messages: JobInfo["messages"] }> {
  let jobStatus = jobInfo.jobStatus;
  let messages = jobInfo.messages;
  const jobId = jobInfo.jobId;
  try {
    jobInfo = await jobInfo.cancelJob(jobId);
    /**
     * Here we are using checkJobStatus to ping the jobStatus every half of a second while the job is cancelling.
     * This is a similar call that waitForJobCompletion is using.
     * Note that checkJobStatus actually has a 60 second built in time out which should handle breaking this loop
     * https://devtopia.esri.com/WebGIS/arcgis-js-api/issues/54116
     * NOTE: There is also an issue in the JSAPI to fix `waitForJobCompletion` as it is not returning
     * the cancelled state as expected.
     */
    // Only allow the cancelling loop if the cancelJob returns `job-cancelling` or is in a
    // completed state.
    // Raster backend issue for this: https://devtopia.esri.com/WebGIS/arcgis-web-analysis/issues/4005
    if (isStatusCancellable(jobInfo.jobStatus) === true || jobInfo.jobStatus === AnalysisJobStatus.Canceling) {
      while (CompletedStatuses.includes(jobInfo.jobStatus) === false) {
        await new Promise((resolve) => setTimeout(resolve, 500));
        jobInfo = await jobInfo.checkJobStatus();
        // notify on every update while cancelling
        notifyJobStatus(containerElement, { jobInfo });
      }
    }
    jobStatus = jobInfo.jobStatus;
    messages = jobInfo.messages;
    /**
     * There are times where (specifically in AGOL env) cancels will happen immediately, skipping
     * the cancelling state. This is okay as long as the job gets cancelled but since the above while
     * loop will not be triggered (as it skips straight to job-cancelled) we should notify the status
     * one last time to ensure the UI updates accordingly.
     */
    notifyJobStatus(containerElement, { jobInfo });
  } catch (e) {
    if (e.jobStatus !== undefined && e.jobId !== undefined && e.messages !== undefined && e.jobId === jobId) {
      jobStatus = e.jobStatus;
      messages = e.messages;
    }
  }
  return { jobStatus, messages };
}
