import memoize from "fast-memoize";

import { AnalysisToolUIRule } from "analysis-ui-schema";

import { isEmptyDataItem, request } from "../common";
import { SupportedLocales } from "../localization/interfaces";
import { AnalyzeGeocodeResult, analyzeGeocodeInput } from "../index";

type GetToolUISourceCountryParams = {
  geocodeServiceURL?: string;
  locale: SupportedLocales;
};

export type GetToolUIAddressFieldsParams = LayerParams | FileParams;

type AnalyzeGeocodeParams = {
  asyncGeocodeUrl: string;
  geocodeServiceURL?: string;
  locale: SupportedLocales;
};

export type LayerParams = AnalyzeGeocodeParams & {
  inputTable?: {
    url: string;
    serviceToken?: string;
  };
  inputFileItem?: never;
  inputFileParameters?: never;
};

export type FileParams = AnalyzeGeocodeParams & {
  inputFileItem?: { itemid: string };
  inputFileParameters?: InputFileParams;
  inputTable?: never;
};

type InputFileParams = {
  fileType: "csv" | "xlsx";
  headerRowExists: "true" | "false";
  columnDelimiter: "SPACE" | "TAB" | "COMMA" | "PIPE" | "SEMICOLON" | "";
  textQualifier: "SINGLE_QUOTE" | "DOUBLE_QUOTE" | "";
};

// TODO: hard coded fields to simplify writing the rules since they are also hardcoded in `batchgeocode.tool.ui.json`.
// Ideally, we need to get the address fields from the Geocoder and build UIParameters dynamically.
const addressFields = [
  "SingleLine",
  "Address",
  "Address2",
  "Address3",
  "Neighborhood",
  "City",
  "Subregion",
  "Region",
  "Postal",
  "PostalExt",
  "CountryCode"
];

const countrySerializerFn = (args: any[]): string => {
  // memoize typings has args as any[] but we receive an object
  // so doing this cast to use the locator URL as the cache key when it is defined.
  const { geocodeServiceURL } = args as unknown as GetToolUISourceCountryParams;
  return geocodeServiceURL ?? "EMPTY_KEY";
};

const addressFieldsSerializerFn = (args: any[]): string => {
  // memoize typings has args as any[] but we receive an object
  // so doing this cast to use the locator URL + input as the cache key when they are defined.
  const { geocodeServiceURL, inputFileItem, inputTable } = args as unknown as GetToolUIAddressFieldsParams;
  const itemid = inputFileItem?.itemid;
  const url = inputTable?.url;
  return !isEmptyDataItem(geocodeServiceURL) && (!isEmptyDataItem(itemid) || !isEmptyDataItem(url))
    ? `${geocodeServiceURL}${itemid}${url}`
    : "EMPTY_KEY";
};

/**
 * This helper function for batch geocode does an asynchronous request for the selected
 * locator (geocodeServiceURL) to get its country list , which is then returned as country rule.
 * @param params
 * @returns {AnalysisToolUIRule} returns the rule for sourceCountry parameter.
 */
export const getToolUIJsonWithSourceCountryRule = memoize(
  async (params: GetToolUISourceCountryParams): Promise<AnalysisToolUIRule> => {
    const { geocodeServiceURL, locale } = params;
    let countryRule: AnalysisToolUIRule;
    if (isEmptyDataItem(geocodeServiceURL)) {
      countryRule = {
        ruleType: "expression",
        expression: "$[Data].geocodeServiceURL = NULL",
        destination: "sourceCountry",
        effectParams: {
          visible: false
        }
      };
    } else {
      let countries;
      let detailedCountries;
      try {
        ({
          data: { countries, detailedCountries }
        } = await request(`${geocodeServiceURL}?f=json`, { responseType: "json" }));
      } catch (err) {
        console.error("Failed to get Locator info");
        throw err;
      }
      const labels: { [country: string]: string } = {};
      detailedCountries.forEach((country: { name: string; localizedNames: { [key: string]: string } }) => {
        // handle all cases where the user locale and the localized country names can be either 2 or 4 letter codes (i.e. "en-US" / "en")
        const localizedNames = country.localizedNames;
        const fourLetterLocale = locale.length > 2 && locale;
        let localizedName: string | undefined;
        if (typeof fourLetterLocale !== "boolean") {
          localizedName = localizedNames[fourLetterLocale];
        }
        if (localizedName === undefined) {
          const twoLetterLocale = locale.length === 2 ? locale.slice(0, 2) : locale;
          localizedName = localizedNames[twoLetterLocale];
          if (localizedName === undefined) {
            const lang = Object.keys(localizedNames).find((key) => key.startsWith(twoLetterLocale));
            if (lang !== undefined) {
              localizedName = localizedNames[lang];
            }
          }
        }
        if (localizedName === null || localizedName === undefined || localizedName === "") {
          // worst case scenario attempt to set the country label to English
          localizedName = localizedNames[SupportedLocales.English] ?? localizedNames[SupportedLocales.EnglishUSA];
        }
        labels[country.name] = localizedName;
      });
      countryRule = {
        ruleType: "expression",
        expression: "$[Data].geocodeServiceURL NOT null",
        destination: "sourceCountry",
        effectParams: {
          visible: true,
          choiceList: countries,
          choiceListLabels: labels
        }
      };
    }
    return countryRule;
  },
  {
    serializer: countrySerializerFn
  }
);

/**
 * This helper function for batch geocode does an asynchronous request for the selected
 * locator (geocodeServiceURL) and input data to get the suggested field mapping, which is then
 * returned as address rule alongside the original response of `AnalyzeGeocodeInput`.
 * @param params
 * @returns {{AnalysisToolUIRule, AnalyzeGeocodeResult}} returns the rule for geocodeUIParameters parameter, and the original response of `AnalyzeGeocodeInput`.
 */
export const getAddressFieldMappingRules = memoize(
  async (
    params: GetToolUIAddressFieldsParams
  ): Promise<{ addressFieldsRule?: AnalysisToolUIRule; geocodeParameters?: AnalyzeGeocodeResult }> => {
    const { geocodeServiceURL, inputFileItem, inputTable } = params;
    let addressFieldsRule: AnalysisToolUIRule | undefined;
    let geocodeParameters: AnalyzeGeocodeResult | undefined;
    if (!isEmptyDataItem(geocodeServiceURL) && (!isEmptyDataItem(inputFileItem) || !isEmptyDataItem(inputTable))) {
      let fieldInfo: Array<[string, string, number]>;
      let fieldMapping: Array<[string, string]>;
      geocodeParameters = await analyzeGeocodeInput(params);
      try {
        fieldInfo = JSON.parse(geocodeParameters.field_info);
        fieldMapping = JSON.parse(geocodeParameters.field_mapping);
      } catch (err) {
        console.error("Failed to parse result of AnalyzeGeocodeInput");
        throw err;
      }
      const choiceList: string[] = fieldInfo.map((field) => field[0]);
      const choiceListLabels = choiceList.reduce(
        (acc: { [key: string]: string }, curr: string) => ({ ...acc, [curr]: curr }),
        {}
      );
      addressFieldsRule = {
        ruleType: "expression",
        expression:
          "($[Data].geocodeServiceURL NOT null) AND (($[Data].inputTable NOT null) OR ($[Data].inputFileItem NOT null))",
        destination: `geocodeUIParameters`,
        effectParams: addressFields.reduce((acc, curr, i) => {
          const value = fieldMapping.find((mapping) => mapping[1] === curr)?.[0];
          return {
            ...acc,
            [`UIparameterInfos[${i}].choiceList`]: choiceList,
            [`UIparameterInfos[${i}].choiceListLabels`]: choiceListLabels,
            [`UIparameterInfos[${i}].defaultValue`]: value
          };
        }, {})
      };
    }
    return { addressFieldsRule, geocodeParameters };
  },
  {
    serializer: addressFieldsSerializerFn
  }
);
